<template>
	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>我的预约留言</p>
		<div class="textBox">
			<img src="static/image/q10jd.png" class="jd" />
			<img v-show="onblur" src="static/image/q10msg.png" class="msg" />
			<textarea class="textarea" v-on:focus="foucus()" v-model="notes"></textarea>
		</div>
		<div class="addAppointment" v-on:click="addAppointment">预约</div>
	</div>
</template>

<script>
	import $ from '../../../static/script/jquery/jquery-2.1.1.min.js'
	
	const host = 'https://api.0-fashion.com'

	const addYy = '/API/Members/Appointment/AddV2'
	var MemberId = window.localStorage.getItem('MemberId'),
		U_Num = window.localStorage.getItem('u_num');

	export default {
		data() {
			return {
				num: 10,
				onblur: 1,
				notes: this.$store.state.notes
			}
		},
		computed: {
			storeRoute() {
				return this.$store.state
			},
			likes() {
				var likes = [];
				for(var i = 0; i < this.storeRoute.likeCloths.length; i++) {
					for(var item in this.storeRoute.likeCloths[i]) {
						likes.push(item)
					}
				}
				likes = likes.join(',')
				return likes;
			},
			dislikes() {
				var dislikes = [];
				for(var i = 0; i < this.storeRoute.dislikeCloths.length; i++) {
					for(var item in this.storeRoute.dislikeCloths[i]) {
						dislikes.push(item)
					}
				}
				dislikes = dislikes.join(',')
				return dislikes;
			},
			options() {
				return JSON.stringify({
					Quantity: parseInt(this.storeRoute.clothCount / 5) || 1,
					Top: this.storeRoute.Top,
					Bottom: this.storeRoute.Bottom,
					Jumpsuit: this.storeRoute.Jumpsuit,
					Budget: this.storeRoute.budGet,
					Situation: this.storeRoute.Situation.join(','),
					Style: this.storeRoute.likeStyle.join(','),
					Color: this.storeRoute.likeColor.join(','),
					Fabric: this.storeRoute.materialId.join(','),
					Season: this.storeRoute.seasonId
				})
			},
			index() {

			}

		},
		methods: {
			foucus() {
				this.onblur = 0;
			},
			addAppointment() {
				var _self = this;
				var data = {
					MemberId: MemberId,
					U_Num: U_Num,
					likes: this.likes,
					dislikes: this.dislikes,
					notes: this.notes,
					options: this.options,
				}
				//			console.log(data)
				$.ajax({
					type: "post",
					url: host + addYy,
					data: data,
					success: function(data) {
						if(data.ResultType == 3) {
							_self.$layer.alert("您的预约已提交成功，搭配师将在两个工作日内配置好您的方案，请耐心等待~", function() {
								//回到上一页
								window.location.href = _self.storeRoute.index;
							});
						} else {
							_self.$layer.msg(data.Message);

						}

					}
				});
			}
		},

		watch: {
			'notes' () { //监听留言修改全局
				this.$store.commit('changeNotes', this.notes);
			}
		}

	}
</script>

<style scoped>
	.notify .notify-content {
		color: #000;
	}
	
	.pagebox {
		height: 80%;
	}
	
	.textBox {
		width: 80vw;
		height: 105vw;
		margin: 0 auto;
		background-image: url(../../../static/image/q10bg.png);
		background-size: cover;
		padding: 10%;
		box-sizing: border-box;
		position: relative;
	}
	
	.jd {
		position: absolute;
		top: -20px;
		left: -20px;
		width: 50px;
	}
	
	.msg {
		position: absolute;
		top: 20%;
		left: 20%;
		width: 60%;
	}
	
	.textarea {
		position: relative;
		background-color: transparent;
		resize: none;
		width: 100%;
		height: 100%;
		border: none;
		outline: none;
		z-index: 1000;
	}
	
	.addAppointment {
		position: fixed;
		bottom: 30px;
		width: 33.3%;
		left: 33.3%;
		border: 1px solid #FFF;
		border-radius: 2px;
		padding: 10px 0;
		text-align: center;
	}
</style>


