<template>
	<div class="bgBody">
		<div class="yuyueYd" data-int='0'></div>
		<div class="huodongYd" data-int='0'>
			<div class="huodBtn"></div>
		</div>
		<div class="bagfil" v-bind:class="{'blur':shop_dialog_show || clear_cache_show || shop_list_show}">
			<!--头像-->
			<div class="touXiang">
				<div class="style_test">
					<a href="static/store/yuyue.html">
						<img src="static/image/yuyue/yuyue@2x.png" alt="" />
						<p>预约</p>
					</a>

				</div>
				<div class="photo">
					<div class="head_portrait">
						<img v-bind:src="UserPhoto" alt="" v-on:click="clear_cache_show = true" />
					</div>
				</div>
				<div class="activity">
					<a href="static/store/huodong/activeIdnex.html">
						<img src="static/image/singleitem/huod@2x.png" alt="" />
						<p>活动</p>
					</a>

				</div>
			</div>
			<!--姓名 等级-->
			<ul class="user_class">
				<li class="right_align name"><span>{{MemberName}}</span></li>
				<!--<li><img src="img/selfmesg/note@2x.png" alt="" /></li>-->
				<li class="right_align grade_style"><span>{{MemberTypeName}}</span></li>
				<!--<li class="grade_style hand_testing">VIP 5</li>-->
			</ul>
			<!--积分 储值-->
			<ul class="integral">
				<li class="integral_details">
					<p class="ingall">{{Score}}</p> <span>积分</span></li>
				<li class="change_store" v-on:click="shop_dialog_show = true">
					<p class="ingallImg">
						<img src="static/image/selfmesg/dp@2x.png" alt="" />
					</p>
					<span>{{StoreName}}</span>
				</li>
				<li class="stored_details">
					<p class="stored_value">{{Balance}}</p> <span>储值</span></li>
			</ul>
			<!--优惠券-->
			<div id='youhq' class="scroll">
				<div class="youhuiquan">
					<img class="barcodeImg" src="" />
					<p>{{CardNumber}}</p>
				</div>
				<div id="newYouh">
					<div class="youhuq">
						<img class="imgYouh" v-on:click="coupon_code_show=true;show_cupon_code(item.CouponNumber)" v-for="item in coupon_list" v-bind:data-int="item.CouponNumber" v-bind:src="item.CouponImagePath">
					</div>
				</div>
				<div id='oldYouh' v-show="expired_coupon_title_show">
					<p class="seeYouh" v-on:click="">查看 过期/已使用 优惠券</p>
					<img class="open" src="static/image/public/sj@2x.png" alt="" />
					<div id="appendYouh">
						<img v-for="item in expired_coupon" class="oldImg" v-bind:data-int='item.CouponNumber' src=''>
					</div>
				</div>
			</div>
			<!--<div class="clearLocalst">
				清除缓存数据
			</div>-->
		</div>
		<!--点击头像退出、清除缓存-->
		<div class="clear_cache" v-show="clear_cache_show">
			<p>温馨提示</p>
			<span>确定退出登录吗?</span>
			<div class="btns">
				<b class="abolish" v-on:click="clear_cache_show = false">取消</b>
				<b class="cache_del" v-on:click="clearCache()">缓存</b>
				<b class="determine" v-on:click="loginOut()">确定</b>
			</div>
		</div>
		<!--提示框-->
		<div class="dialog_box Login-failure" v-show="login_failure">
			<p>温馨提示</p>
			<span>{{login_fail_msg}}</span>
			<div class="close_dialog  souquan" type="button" v-on:click="loginOut()">确定</div>
		</div>
		<div class="tryClothes" v-show="shop_dialog_show">
			<p>温馨提示</p>
			<span>切换店铺将清除所有积分</span>
			<div type="button" class="cancel" v-on:click="shop_dialog_show = false">取 消</div>
			<div type="button" class="sure" v-on:click="getAllShop();shop_dialog_show=false;shop_list_show=true">确 定</div>
		</div>
		<!--<div class="loginSure" style="display: none;">
			<p>温馨提示</p>
			<span>确定登陆</span>
			<div type="button" class="cancel refuse">拒 绝</div>
			<div type="button" class="sure adopt">确 定</div>
		</div>-->
		
		<!--更改店铺-->
		<div class="changeStore" v-show="shop_list_show" v-on:click="shop_list_show=false">
			<ul class="chang_shop scroll" >
			<li v-on:click="changeStore(item.Id)" v-for="item in shop_list">{{ item.StoreName }}</li>
		</ul>
		</div>
		
		<!--<div class="bg_dialog"></div>-->
		<!--优惠券弹窗-->
		<div class="yhqDialog" v-show="coupon_code_show" v-on:click="coupon_code_show=false">
			<!--优惠券遮罩-->

			<div class="youhuiquanalog">

				<img class="barcodeImgalog" />
				<p>{{coupon_code_show_num}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	import $ from '../../../static/script/jquery/jquery-2.1.1.min.js'


	var host = 'https://api.0-fashion.com';
	var options = {
		format: "CODE128", //选择要使用的条形码类型
		width: 4, //设置条之间的宽度
		height: 100, //高度
		displayValue: false, //是否在条形码下方显示文字
		background: "#fff", //设置条形码的背景
		lineColor: "#000", //设置条和文本的颜色。
		margin: 0 //设置条形码周围的空白边距
	};

	export default {
		data() {
			return {
				UserPhoto: localStorage.UserPhoto,
				MemberName: localStorage.MemberName,
				StoreName: '我的店铺',
				MemberTypeName: '',
				Score: '',
				Balance: '',
				clear_cache_show: false,
				shop_dialog_show: false,
				shop_list_show: false,
				login_failure: false,
				login_fail_msg: '',
				shop_list: [],
				coupon_list: [],
				expired_coupon: [],
				CardNumber: "",
				coupon_code_show: false,
				coupon_code_show_num: ''

			}
		},
		computed: {
			expired_coupon_title_show() {
				if(this.expired_coupon.length > 0) {
					return true;
				} else {
					return false;
				}
			}
		},
		mounted: function() {

			this.getUserMessage()
			this.getUserStore()
			this.getCoupon()
			this.getExpiredcoupon(1);
			this.getExpiredcoupon(2);

		},

		methods: {
			getUserMessage() {
				var _self = this;
				//获取用户信息
				$.ajax({
					type: "post",
					url: host + "/api/Members/MemberInfo/GetMemberName",
					data: {
						MemberId: localStorage.MemberId,
						U_Num: localStorage.u_num,
					},
					success: function(data) {
						if(data.ResultType == 3) {
							_self.MemberTypeName = data.Data.MemberTypeName;
							//$(".hand_testing").html(data.Data.LevelName);
							_self.Score = data.Data.Score;
							_self.Balance = data.Data.Balance;
						} else {
							_self.login_failure = true;
							_self.login_fail_msg = data.Message;
							//showDiag(data.Message);
						}
					}
				});
			},
			getUserStore() {
				var _self = this;
				$.ajax({
					type: "post",
					url: host + '/api/Members/MemberInfo/GetMemberStore',
					data: {
						MemberId: localStorage.MemberId,
						U_Num: localStorage.u_num,
					},
					success: function(data) {
						if(data.ResultType == 3) {
							$.each(data.Data, function(i, item) {
								if(item.IsMainStore == false) {
									_self.StoreName = item.StoreName;
								}
							});
						}
					}
				});
			},
			loginOut() {
				localStorage.removeItem('MemberId');
				localStorage.removeItem('u_num');
				localStorage.removeItem('UserPhoto');
				localStorage.removeItem('MemberName');
				//回到登录
				window.location.href = this.$store.state.login;

			},
			clearCache() {
				localStorage.clear();
				//回到登录
				window.location.href = this.$store.state.login;
			},
			getAllShop() {
				var _self = this;
				$.ajax({
					type: "post",
					url: host + "/API/stores/store/queryattachstore",
					data: {
						MemberId: localStorage.MemberId,
						U_Num: localStorage.u_num,
					},
					success: function(data) {
						if(data.ResultType == 3) {
							_self.shop_list = data.Data

						}

					}
				})
			},
			changeStore(shopId) {
				var _self = this;
				$.ajax({
					type: "post",
					url: host + "/api/members/member/SwitchAttachStore",
					data: {
						MemberId: localStorage.MemberId,
						U_Num: localStorage.u_num,
						newStoreId: shopId,
					},
					success: function(da) {
						if(da.ResultType == 3) {
							_self.getUserStore()
						}
					}
				});

			},
			getCoupon() {
				var _self = this;
				//未使用
				$.ajax({
					type: "post",
					url: host + "/api/members/mywallet/get",
					data: {
						MemberId: localStorage.MemberId,
						U_Num: localStorage.u_num,
						CouponType: 0
					},
					success: function(data) {
						//var divBox = '<div class="youhuiquan"><div class="dengJi"><img class="loYh" src="img/public/logo@2x.png" alt="" /><span> VIP</span> <i>5级</i><b>优惠8折</b></div><img class="barcodeImg" /><p></p></div>';

						if(data.ResultType == 3) {
							_self.coupon_list = data.Data.ListCouPon;
							_self.CardNumber = data.Data.CardNumber;
							JsBarcode(".barcodeImg", _self.CardNumber, options);

							//$("#newYouh").prepend(divBox);
							//$(".youhuiquan p").html(data.Data.CardNumber);
							//$(".barcodeImg").JsBarcode(data.Data.CardNumber, options);

							//							$.each(data.Data.ListCouPon, function(i, item) {
							//								coupon_list
							//								$(".youhuq").prepend('<img class="imgYouh" data-int=' + item.CouponNumber + '  src=' + item.CouponImagePath + '>')
							//							});
							//							$(".barcodeImg").click(function() {
							//								saomiao(data.Data.CardNumber);
							//							});
							//							$(".imgYouh").bind('click', function() {
							//								var $tiao = $(this).attr('data-int');
							//								saomiao($tiao);
							//							});
						}
					}
				});
			},
			getExpiredcoupon(type) {
				var _self = this;
				$.ajax({
					type: "post",
					url: host + "/api/members/mywallet/get",
					data: {
						MemberId: localStorage.MemberId,
						U_Num: localStorage.u_num,
						CouponType: type
					},
					success: function(data) {
						if(data.ResultType == 3) {
							_self.expired_coupon = data.Data.ListCouPon;

							//								$.each(data.Data.ListCouPon, function(i, item) {
							//									$("#appendYouh").append('<img class="oldImg" data-int=' + item.CouponNumber + '  src=' + item.CouponImagePath + '>');
							//									past++;
							//								});
							//								$(".oldBarcodeImg,.oldImg").click(function() {
							//									if(type == 1) {
							//										layer.msg("已使用");
							//									} else {
							//										layer.msg("已过期");
							//									}
							//								});
							//								num += past;
							//								if(num == 0) {
							//									$('#oldYouh').hide();
							//								} else {
							//									$('#oldYouh,.open').show();
							//									var flag = 1;
							//									$(".open,.seeYouh").click(function() {
							//										if(flag == 1) {
							//											$("#appendYouh").show();
							//											$('.open').hide();
							//											flag = 0;
							//										} else {
							//											$("#appendYouh").hide();
							//											$('.open').show();
							//											flag = 1;
							//										}
							//									});
							//								}
						}
					}
				});
			},
			show_cupon_code(code) {

				JsBarcode(".barcodeImgalog", code, options);
				this.coupon_code_show_num = code
			}
		}
	}
</script>

<style scoped>
	/*头像*/
	.blur{
		filter:blur(3px)
	}
	.photo {
		width: 50%;
		text-align: center;
		float: left;
	}
	
	.head_portrait {
		width: 30vw;
		height: 30vw;
		border-radius: 50%;
		border: 5px solid rgba(255, 255, 255, 0.4);
		margin: 5px auto 0;
		overflow: hidden;
	}
	
	.head_portrait img {
		width: 100%;
		height: 100%;
	}
	/*风格测试*/
	
	.style_test,
	.activity {
		width: 25%;
		float: left;
		font-size: 0.3rem;
		color: #fff;
		margin-top: 15vw;
	}
	
	.style_test a,
	.activity a {
		color: #fff;
		text-decoration: none;
	}
	
	.style_test {
		text-align: right;
	}
	
	.activity {
		text-align: left;
	}
	
	.style_test p {
		padding-right: 1vw;
		font-size: 12px;
		text-align: right;
	}
	
	.activity p {
		font-size: 12px;
		width: 8vw;
		text-align: center;
	}
	
	.style_test img {
		width: 8vw;
	}
	
	.activity img {
		width: 8vw;
	}
	
	.gift_box {
		width: 25%;
		float: right;
	}
	/*等级*/
	
	.user_class {
		width: 100%;
		color: #fff;
		clear: both;
	}
	
	.user_class li {
		width: 100%;
		font-size: 14px;
		padding-top: 10px;
		text-align: center;
	}
	
	.user_class li img {
		width: 0.3rem;
		height: 0.35rem;
		margin-left: 0.8rem;
		vertical-align: middle;
	}
	
	.hand_testing {
		text-indent: 0.35rem;
	}
	/*积分储值*/
	
	.integral {
		width: 100%;
		height: 12vw;
		overflow: hidden;
		color: #fff;
		margin-top: 4vw;
		background: rgba(255, 255, 255, 0.5);
	}
	
	.change_store {
		width: 29.3%;
		text-align: center;
		float: left;
	}
	
	.integral_details,
	.stored_details {
		width: 35%;
		float: left;
		text-align: center;
		height: 0.88rem;
	}
	
	.integral_details,
	.change_store {
		border-right: 1px solid rgba(255, 255, 255, 0.5);
	}
	
	.integral li {
		list-style: none;
		height: 100%;
	}
	
	.integral li p {
		font-size: 14px;
		line-height: 20px;
	}
	
	.integral li .ingallImg img {
		width: 4vw;
		text-align: center;
		vertical-align: top;
		margin-top: 5px;
	}
	
	.integral li span {
		display: block;
		font-size: 12px;
	}
	/*会员区域*/
	
	.vip_img {
		width: 100%;
		margin: 0.22rem auto;
	}
	
	.chang_shop {
		width: 30vw;
		height: 50vw;
		padding: 10px;
		font-size: 14px;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -20vw;
		margin-top: -30vw;
		background: rgba(0, 0, 0, 0.6);
		border-radius: 5px;
		z-index: 12;
		overflow-y: scroll;
	}
	
	.chang_shop li {
		color: #fff;
		width: 100%;
		text-align: center;
	}
	
	.bg_dialog {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		background: transparent;
		z-index: 10;
		display: none;
	}
	
	p {
		text-align: center;
	}
	
	.youhqBg,.changeStore {
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		position: fixed;
		z-index: 10;
		top: 0;
		left: 0;
	}
	
	.yhqDialog {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 1000;
		background: rgba(0, 0, 0, .3);
	}
	
	#youhq {
		overflow-y: scroll;
	}
	
	.youhuiquan {
		padding-bottom: 1rem;
	}
	
	.barcodeImg,
	.barcodeImgalog,
	.oldBarcodeImg,
	.pastBarcodeImg {
		margin-top: 10px;
		vertical-align: top;
	}
	
	.barcodeImgalog {
		display: block;
		height: 15vh;
	}
	
	.barcodeImg,
	.barcodeImgalog,
	.pastBarcodeImg {
		width: 80%;
		margin-left: 10%;
	}
	
	.oldBarcodeImg,
	.pastBarcodeImg {
		width: 90%;
		margin-left: 5%;
	}
	
	.youhuiquanalog {
		width: 80%;
		background: #FFF;
		border-radius: 5px;
		margin: 30vh auto;
		padding: 20px 0;
	}
	
	.youhuiquanalog p {
		color: #000;
	}
	
	.imgYouh,
	.oldImg,
	.pastImg {
		/*height: 2rem;*/
		padding-bottom: 1rem;
	}
	
	.oldImg,
	.pastImg {
		width: 90%;
		margin-left: 5%;
	}
	
	.imgYouh {
		width: 80%;
		margin-left: 10%;
	}
	
	#oldYouh {
		width: 90%;
		margin: 0 auto;
		margin-bottom: 0.6rem;
		border: 1px dotted #fff;
		background-color: rgba(255, 255, 255, 0.2);
	}
	
	#oldYouh .seeYouh {
		font-size: 0.3rem;
		color: #FFF;
		text-align: center;
		padding: 0.2rem 0;
	}
	
	.open {
		display: block;
		margin: 0 auto;
		width: 0.3rem;
		padding-bottom: 0.2rem;
	}
	
	#appendYouh {
		display: none;
	}
	/*提示框*/
	
	.dialog_box,
	.tryClothes,
	.clear_cache,
	.dialogShow {
		background: rgba(255, 255, 255, .8);
		color: #000;
		width: 50vw;
		border-radius: 5px;
		position: absolute;
		left: 50%;
		top: 50%;
		margin-left: -25vw;
		margin-top: -15vw;
		/*display: none;*/
		z-index: 15;
	}
	/*.clear_cache{display: block;}*/
	
	.dialog_box,
	.dialogShow {
		background: #fff;
	}
	
	.dialog_box p,
	.tryClothes p,
	.clear_cache p,
	.dialogShow p {
		font-size: 12px;
		color: #000;
		text-align: center;
		padding-top: 0.54rem;
	}
	
	.tryClothes p {
		border-radius: 5px 5px 0 0;
	}
	
	.dialog_box span,
	.tryClothes span,
	.clear_cache span,
	.dialogShow span {
		font-size: 12px;
		color: #000;
		display: block;
		width: 100%;
		text-align: center;
		padding-top: 0.26rem;
	}
	
	.clear_cache .btns {
		margin-top: 15px;
		border-top: 1px solid rgba(0, 0, 0, .1);
	}
	
	.clear_cache b {
		padding: 5px 0;
		font-weight: normal;
		width: 33.3%;
		display: block;
		float: left;
		font-size: 12px;
		box-sizing: border-box;
		text-align: center;
	}
	
	.clear_cache b:nth-child(2) {
		border-left: 1px solid rgba(0, 0, 0, .1);
		border-right: 1px solid rgba(0, 0, 0, .1);
	}
	
	.tryClothes div[type=button] {
		float: left;
		width: 50%;
		margin-top: 15px;
		box-sizing: border-box;
		font-size: 12px;
		padding: 5px 0;
		text-align: center;
		border-top: 1px solid rgba(0, 0, 0, .1);
	}
	
	.tryClothes div.cancel {
		border-right: 1px solid rgba(0, 0, 0, .1);
	}
	
	.close_dialog {
		padding: 5px 0;
		border-top: 1px solid rgba(0, 0, 0, .1);
		text-align: center;
		margin-top: 15px;
	}
	
	.scroll {
		height: 50vh;
		overflow: scroll;
	}
</style>


