<template id="question">
	<div class="pagebox" id="movebox" >
		<p class="question-subject"><span class="questionNum">1</span>请选择预约件数</p>
		<div class="select-cloth-count">
			<div>{{ SroreClothCount }}件</div>
			<img class="cloth" src="static/image/cloth@2x.png" />
			<div class="rulebox">
				<ul class="rule">
					<li><span class="ScaleNum">1</span></li>
					<li></li>
					<li></li>
					<li></li>
					<li class="pointScale"><span class="ScaleNum">5</span></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li class="pointScale"><span class="ScaleNum">10</span></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li class="pointScale"><span class="ScaleNum">15</span></li>
				</ul>
				<div  @touchstart="dragstart($event)" :style="{left:(SroreClothCount-1) * spacing+'px'}" class="pointer"></div>

			</div>
		</div>
		<router-link class="next-subject" to="/appointmentSelf/question2">NEXT</router-link>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				msg: 'question1',
				clientXstart: 0,
				startLeft: 0,
				spacing: 16,
			}
		},
		computed: {
			SroreClothCount() {
				return this.$store.state.clothCount
			}
		},
		methods: {
			dragstart: function(ev) {
				this.clientXstart =  ev.touches[0].clientX || ev.clientX;
				this.startLeft = document.querySelector('.pointer').style.left.replace("px", "");
				document.getElementById('movebox').onmousemove = this.dragmove;
				document.getElementById('movebox').onmouseup = this.dragend;
				document.getElementById('movebox').addEventListener('touchmove',this.dragmove)
				document.getElementById('movebox').addEventListener('touchend', this.dragend)
			},
			dragmove: function(ev) {					
				var clientX = ev.touches[0].pageX || ev.clientX;
				var deviationX = parseFloat(clientX) - parseFloat(this.clientXstart);
				var left = parseFloat(deviationX) + parseFloat(this.startLeft);
				document.querySelector('.pointer').style.left = left + "px";
			},
			dragend: function(ev) {
				document.getElementById('movebox').onmousemove = document.getElementById('movebox').ontouchmove = null;
				document.getElementById('movebox').onmouseup = document.getElementById('movebox').ontouchend = null;

				var currentLeft = document.querySelector('.pointer').style.left.replace("px", "");
				var Count = Math.round(currentLeft / this.spacing) + 1;

				if(Count < 1) {
					Count = 1
				} else if(Count > 15) {
					Count = 15
				}

				this.$store.commit('changeClothCount', Count);

				document.querySelector('.pointer').style.left = (Count - 1) * 16 + "px"

			}
		}
	}
</script>

<style scoped>
	ul,
	li {
		list-style: none;
	}

	/*question1*/
	
	.select-cloth-count img.cloth {
		width: 45%;
		display: block;
		margin: 30px auto;
	}
	
	.select-cloth-count .pointer {
		width: 30px;
		height: 50px;
		position: absolute;
		top: 100%;
		margin-left: -11px;
		left: 0;
		background: url(../../../static/image/clothappointment.png);
		background-size: 100%;
		background-repeat: no-repeat;
		cursor: pointer;
	}
	
	.select-cloth-count .pointer img {
		width: 100%;
	}
	
	.select-cloth-count {
		text-align: center;
	}
	
	.rulebox {
		display: inline-block;
		position: relative;
	}
	
	.select-cloth-count .rule {
		height: 40px;
		margin-top: 50px;
		display: inline-block;
	}
	
	.select-cloth-count .rule li {
		float: left;
		width: 8px;
		height: 30px;
		background: #b8b8ba;
		margin-right: 8px;
		position: relative;
	}
	
	.select-cloth-count .rule li.pointScale {
		height: 40px;
		background: #ffffff;
	}
	
	.ScaleNum {
		position: absolute;
		top: -30px;
		left: 0;
	}
</style>


