<template>
	<div id="app">
		<router-view></router-view>
	</div>
</template>

<!--根组件-->

<script>
	export default {
		name: 'app',
		created: function() { //实例创建完成后的钩子函数
			//this.$router.push('/');//不保存路由，刷新回到默认页
		}
	}
</script>

<style>
	*{
		margin: 0;
		padding: 0;
	}
	html,
	body,
	#app {
		
		width: 100%;
		height: 100%;
		-moz-user-select: none;
		-webkit-user-select: none;
		-ms-user-select: none;
		color: #FFF;
		overflow: hidden;
	}
</style>


