<template>

	<div class="pagebox">

		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择风格</p>
		<img class="timez" src="static/image/q6g.png" />
		<div class="stylebox">
			<div class="style" v-for="(style,index) in styleList" v-on:click="clickStyle(index)" v-bind:class="{'active':style.checked == 1}">
				{{ style.name }}
			</div>
		</div>
		<a class="next-subject" href="#/appointmentSelf/question7">NEXT</a>
	</div>
</template>
<script>
	export default {
		name: 'question7',
		data() {
			return {
				num: 6,
				styleList: [{
						id: 0,
						name: "不限",
						checked: 0,
						all: true
					},
					{
						id: 1,
						name: "基础商务",
						checked: 0
					},
					{
						id: 2,
						name: "时尚商务",
						checked: 0
					},
					{
						id: 3,
						name: "时尚达人",
						checked: 0
					},
					{
						id: 4,
						name: "个性张扬",
						checked: 0
					},
					{
						id: 5,
						name: "优雅端庄",
						checked: 0
					},
					{
						id: 6,
						name: "舒适休闲",
						checked: 0
					}

				]
			}
		},
		created: function() {
			for(var i = 0; i < this.styleList.length; i++) {
				for(var j = 0; j < this.SroreLikeColor.length; j++) {
					if(this.styleList[i].id == this.SroreLikeColor[j]) {
						this.styleList[i].checked = 1;
					}
				}
			}
		},
		computed: {
			//获取全局风格Id
			SroreLikeColor() {
				return this.$store.state.likeStyle
			}
		},
		methods: {
			clickStyle(index) {
				if(this.styleList[index].all) {
					for(var k = 0; k < this.styleList.length; k++) {
						this.styleList[k].checked = 0;

					}
					this.styleList[0].checked = 1;

				} else {
					this.styleList[0].checked = 0;
					this.styleList[index].checked = !this.styleList[index].checked;
				}

				this.selectLikeStyle()
			},
			selectLikeStyle() {
				var checkedStyle = [];
				for(var i = 0; i < this.styleList.length; i++) {
					if(this.styleList[i].checked) {
						checkedStyle.push(this.styleList[i].id)
					}
				}
				if( checkedStyle.length <= 0 ) {
					checkedStyle = [0]; //默认不限
				}
				this.$store.commit('changeStyle', checkedStyle);
			}

		}
	}
</script>

<style scoped>
	.timez {
		width: 1.5%;
		position: absolute;
		top: 20%;
		left: 49%;
	}
	
	.style.active {
		background-color: #229c8d;
		border: none;
		padding: 10px 15px;
	}
	
	.style {
		transition: .35s all;
		width: 30%;
		padding: 10px;
		position: absolute;
		border: 1px solid rgba(255, 255, 255, .5);
	}
	
	.style:nth-child(odd) {
		border-radius: 15px 0 0 15px;
		right: 52%;
	}
	
	.style:nth-child(even) {
		border-radius: 0 15px 15px 0;
		left: 52%;
	}
	
	.style:nth-child(1) {
		top: 27%;
	}
	
	.style:nth-child(2) {
		top: 32%;
	}
	
	.style:nth-child(3) {
		top: 37%;
	}
	
	.style:nth-child(4) {
		top: 42%;
	}
	
	.style:nth-child(5) {
		top: 47%;
	}
	
	.style:nth-child(6) {
		top: 52%;
	}
	
	.style:nth-child(7) {
		top: 57%;
	}
</style>


