<template>
	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择场合</p>
		<div class="situationBox">

			<div class="bgbox">
				<div class="situation" :class="{'selected':item.checked}" v-for="(item,index) in situations" v-on:click="selectSituation(index,item.id)">
					<p>{{ item.des }}</p>
					<div class="innerimage"><img v-bind:src="'static/image/q3a'+item.id+'.png'" /></div>
				</div>

			</div>
		</div>
		<a class="next-subject" href="#/appointmentSelf/question4">NEXT</a>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				num: 3,
				situations: [{
						id: "1",
						des: "正式商务",
						checked: 0
					},
					{
						id: "2",
						des: "日常上班",
						checked: 0
					},
					{
						id: "3",
						des: "休闲",
						checked: 0
					},
					{
						id: "4",
						des: "上班偏休闲",
						checked: 0
					},
					{
						id: "5",
						des: "旅行",
						checked: 0
					},
					{
						id: "6",
						des: "PARTY",
						checked: 0
					},

				]
			}
		},
		computed: {
			StoreSituation() {
				return this.$store.state.Situation
			}
		},
		created: function() {
			for(var i = 0; i < this.situations.length; i++) {
				for(var j = 0; j < this.StoreSituation.length; j++) {
					if(this.situations[i].id == this.StoreSituation[j]) {
						this.situations[i].checked = 1;
					}
				}
			}
		},
		methods: {
			selectSituation(index, id) {

				this.situations[index].checked = !this.situations[index].checked

				var selectSituation = [];
				for(var i = 0; i < this.situations.length; i++) {
					if(this.situations[i].checked) {
						selectSituation.push(this.situations[i].id)
					}

				}

				this.$store.commit('changeSituation', selectSituation);

			}
		}

	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.situationBox {
		margin: 20% auto;
		position: relative;
	}
	
	.bgbox {
		position: absolute;
		width: 80%;
		padding-top: 80%;
		top: 0;
		left: 10%;
		/*border: 1px solid #e59814;*/
		border-radius: 50%;
		box-sizing: border-box;
	}
	
	.bgbox img {
		width: 30%;
		position: absolute;
		bottom: -18%;
		left: 35%;
	}
	
	.situation {
		z-index: 1000;
		width: 35%;
		box-sizing: border-box;
		padding-top: 35%;
		position: absolute;
		margin-bottom: 10px;
	}
	
	.situationBox:after {
		content: "";
		display: block;
		clear: both;
	}
	
	.innerimage {
		border: 2px solid #FFF;
		border-radius: 50%;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
	}
	
	.situation img {
		position: absolute;
		top: 20%;
		left: 0;
		right: 0;
		height: 40%;
		display: block;
		margin: 0 auto;
	}
	
	.situation:nth-child(1) {
		top: -17.5%;
		left: 32.5%;
	}
	
	.situation:nth-child(2) {
		top: 8%;
		right: -10%;
	}
	
	.situation:nth-child(3) {
		top: 8%;
		left: -10%;
	}
	
	.situation:nth-child(4) {
		bottom: 8%;
		right: -10%;
	}
	
	.situation:nth-child(5) {
		bottom: 8%;
		left: -10%;
	}
	
	.situation:nth-child(6) {
		bottom: -17.5%;
		right: 32%;
	}
	
	.situation p {
		position: absolute;
		bottom: 18%;
		width: 100%;
		font-size: 12px;
		color: #FFF;
	}
	

	.situation.selected p {
		/*color: #e59814;*/
	}
	
	.situation.selected .innerimage {
		transition: .35s all;
		background: rgba(229,152,20,.1);
		border: 2px solid #e59814;
	}
</style>


