// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import Layer from 'vue-layer'

Vue.prototype.$layer = Layer(Vue);

//全局变量store
Vue.use(Vuex);

//获取querystring
var querystring = {},
	queryselectNum, selectimagesrc, initImage;
var search = location.search.replace('?', '').split('&');
for(var i = 0; i < search.length; i++) {
	var querykey = search[i].split('=')[0];
	querystring[querykey] = search[i].split('=')[1]
}
queryselectNum = querystring.ProductNumber;
selectimagesrc = querystring.imagesrc;
if(queryselectNum && selectimagesrc) {
	initImage = [{
		[querystring.ProductNumber]: querystring.imagesrc
	}];
} else {
	initImage = []
}

const store = new Vuex.Store({
	state: {
		clothCount: 5, //默认衣服件数id
		Top: 0,
		Bottom: 0,
		Jumpsuit: 0,
		Situation: [0], //默认场合
		seasonId: 1, //默认季节
		materialId: [0], //默认布料数组id
		likeColor: [0], //默认颜色
		likeStyle: [0], //默认风格
		budGet: 0, //默认预算
		likeCloths: initImage, //默认喜欢单品
		dislikeCloths: [],
		notes: '', //留言
		index: 'index.html',
		login: 'static/store/login.html',
		productDetial: 'static/store/shangpin.html'
	},
	mutations: {
		//修改衣服件数
		changeClothCount(state, val) {
			state.clothCount = val
		},
		changeTop(state, val) {
			state.Top = val
		},
		changeJumpsuit(state, val) {
			state.Jumpsuit = val
		},
		changeBottom(state, val) {
			state.Bottom = val
		},
		//修改场合
		changeSituation(state, val) {
			state.Situation = val
		},
		//修改季节
		changeSeason(state, id) {
			state.seasonId = id;
		},
		//修改布料
		changeMaterial(state, id) {
			state.materialId = id;
		},
		//修改颜色
		changeColor(state, id) {
			state.likeColor = id;
		},
		//修改颜色
		changeStyle(state, id) {
			state.likeStyle = id;
		},
		//修改预算
		changeBudGet(state, id) {
			state.budGet = id;
		},
		//修改喜欢商城单品(去重)
		changeLikeCloths(state, id) {
			state.likeCloths.push(id);
		},
		//修改不喜欢商城单品(去重)
		changeDisLikeCloths(state, id) {
			state.dislikeCloths.push(id);
		},
		//修改留言
		changeNotes(state, notes) {
			state.notes = notes;
		}
	}
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
	el: '#app',
	store,
	router,
	template: '<App/>',
	components: {
		App
	}
})


