<template>
	<div class="appointmentBox">
		<header v-show="headerShow">
			<div class="back">
				<a href="javascript:;" class="gohistory" onclick="window.history.back();"><img src="static/image/return@2x.png" alt="">返回</a>
				<a v-bind:href="indexHref" class="closeyy"><img src="data:image/png;base64," /></a>
			</div>
			<p>{{ title }}</p>
			<div class="progress">
				<a href="javascript:;" :class="{'active': n <= activeQuestionNum }" v-for="n in TotalNumOfQuestions"></a>
			</div>
		</header>

		<router-view></router-view>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				title: "预约",
				headerShow: true,
				TotalNumOfQuestions: 10,
				activeQuestionNum: 1,
			}
		},
		computed: {
			indexHref() {
				return this.$store.state.index;
			}
		},
		watch: {
			'$route' (to, from) {
				//切换预约路由控制进度灯
				var currentQN = parseInt(to.name.split('_')[1]);
				this.activeQuestionNum = currentQN;
				currentQN ? this.headerShow = true : this.headerShow = false
				this.activeQuestionNum = currentQN
			}
		}
	}
</script>

<style>
	.appointmentBox {
		height: 100%;
		background: url(../../../static/image/dbg.png);
		background-size:cover ;
		text-align: center;
	}
	.swiper-wrapper .swiper-slide .swiper-slide-shadow-left,
	.swiper-wrapper .swiper-slide .swiper-slide-shadow-right,
	.swiper-wrapper .swiper-slide .swiper-slide-shadow-top,
	.swiper-wrapper .swiper-slide .swiper-slide-shadow-bottom	
	{
		background-image: none;
		background-color: transparent;
		
	}
	
	/*header*/
	
	header {
		height: 9%;
		width: 100%;
		position: relative;
		z-index: 100000;
		border-bottom: 1px solid rgba(255, 255, 255, .3);
	}
	
	a {
		text-decoration: none;
		box-shadow: none;
		outline: none;
		color: #FFF;
	}
	
	header .back a.gohistory {
		text-decoration: none;
		font-size: 14px;
		color: #fff;
		position: absolute;
		top: 50%;
		left: 2%;
		width: 20%;
		margin-top: -3px;
	}
	
	.closeyy {
		position: absolute;
		top: 40%;
		right: 0;
		width: 20%;
		margin-top: -3px;
		padding: 3px;
	}
	
	.closeyy img {
		width: 20px;
	}
	
	header .back a.gohistory img {
		width: 7px;
		display: inline;
		height: 12px;
		padding-right: 2px;
		vertical-align: middle;
		margin-top: -2px;
	}
	
	header>p {
		letter-spacing: 2px;
		font-size: 17px;
		height: 16px;
		left: 50%;
		margin-left: -22.5px;
		margin-top: -8px;
		position: absolute;
		top: 50%;
		width: 70px;
		color: #FFFFFF;
	}
	
	.progress {
		width: 100%;
		position: absolute;
		top: 100%;
		margin-top: -1%;
	}
	
	.progress a {
		float: left;
		width: 2%;
		height: 0;
		padding-top: 2%;
		border-radius: 50%;
		margin-left: 6.3%;
		background: rgba(0, 0, 0, .3);
	}
	
	.progress a:nth-child(1) {
		border: 1px solid #D30002;
	}
	
	.progress a:nth-child(1).active {
		background: #D30002;
	}
	
	.progress a:nth-child(2) {
		border: 1px solid #EE5500;
	}
	
	.progress a:nth-child(2).active {
		background: #EE5500;
	}
	
	.progress a:nth-child(3) {
		border: 1px solid #ED9E03;
	}
	
	.progress a:nth-child(3).active {
		background: #ED9E03;
	}
	
	.progress a:nth-child(4) {
		border: 1px solid #F6EB24;
	}
	
	.progress a:nth-child(4).active {
		background: #F6EB24;
	}
	
	.progress a:nth-child(5) {
		border: 1px solid #21D103;
	}
	
	.progress a:nth-child(5).active {
		background: #21D103;
	}
	
	.progress a:nth-child(6) {
		border: 1px solid #2DE8CE;
	}
	
	.progress a:nth-child(6).active {
		background: #2DE8CE;
	}
	
	.progress a:nth-child(7) {
		border: 1px solid #119FCF;
	}
	
	.progress a:nth-child(7).active {
		background: #119FCF;
	}
	
	.progress a:nth-child(8) {
		border: 1px solid #114ACE;
	}
	
	.progress a:nth-child(8).active {
		background: #114ACE;
	}
	
	.progress a:nth-child(9) {
		border: 1px solid #9904E2;
	}
	
	.progress a:nth-child(9).active {
		background: #9904E2;
	}
	
	.progress a:nth-child(10) {
		border: 1px solid #D50D76;
	}
	
	.progress a:nth-child(10).active {
		background: #D50D76;
	}
	
	.pagebox {
		padding: 20px;
	}
	
	.question-subject {
		margin-bottom: 20px;
		text-align: left;
	}
	
	.next-subject {
		position: fixed;
		bottom: 30px;
		width: 33.3%;
		left: 33.3%;
		border: 1px solid #FFF;
		border-radius: 2px;
		padding: 10px 0;
		text-align: center;
		z-index: 9999999;
	}
	
	.questionNum {
		color: #000;
		background: #FFF;
		width: 25px;
		height: 25px;
		display: inline-block;
		vertical-align: middle;
		border-radius: 50%;
		line-height: 25px;
		text-align: center;
		margin-right: 15px;
		font-size: 14px;
	}
</style>


