<template>
	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择您喜欢的单品</p>
		<div class="likeBox">
			<a class="addlike" href="#appointmentSelf/product"><img alt="添加服装" src="data:image/png;base64," />({{SrorelikeCloths.length}}/5)</a>
			<p class="title">喜欢的单品</p>
			<div v-for="(item,index) in SrorelikeCloths" class="likecloth">
				<div v-for="(cloth,productNum) in item" class="likeclothinner">
					<div class="delete" v-bind:productNum="productNum" v-on:click="deleteLikesCloth(productNum,index)"><img alt="删除" src="data:image/png;base64," /></div>
					<img v-bind:src="cloth" />
				</div>
			</div>
		</div>
		<div class="dislikeBox">
			<a class="adddislike" href="#appointmentSelf/disproduct"><img alt="添加服装" src="data:image/png;base64," />({{SroredislikeCloths.length}}/5)</a>
			<p class="title">不喜欢的单品</p>
			<div v-for="(item,index) in SroredislikeCloths" class="likecloth">
				<div v-for="(cloth,productNum) in item" class="likeclothinner">
					<div class="delete" v-bind:productNum="productNum" v-on:click="deleteDisLikesCloth(productNum,index)"><img alt="删除" src="data:image/png;base64," /></div>
					<img v-bind:src="cloth" /></div>
			</div>
		</div>
		
		<a class="next-subject" href="#/appointmentSelf/question6">NEXT</a>
	</div>
</template>

<script>
	
	export default {
		data() {
			return {
				num: '5',
				activeIndex: 0,
				activeCardLike: 0, //当前显示卡片选择喜欢为1，未选择喜欢为0
				activeCardDisLike: 0, //当前显示卡片选择不喜欢为1，未选择不喜欢为0
				cloths: [

				]
			}
		},
		computed: {
			//获取全局变量
			SrorelikeCloths() {

				return this.$store.state.likeCloths;
			},
			SroredislikeCloths() {
				return this.$store.state.dislikeCloths;
			},

		},
		mounted: function() {
			var _self = this;

			var swiper = new Swiper('.swiper-container', {
				pagination: '.swiper-pagination',
				effect: 'coverflow',
				slidesPerView: 2,
				centeredSlides: true,

				onTransitionStart: function(swiper) {
					_self.activeIndex = swiper.activeIndex;
					var activeLike = swiper.slides[swiper.activeIndex].dataset.like;

					if(activeLike == 1) {
						_self.activeCardLike = 1;
					} else if(activeLike == -1) {
						_self.activeCardDisLike = 1;
					} else if(activeLike == 0) {
						_self.activeCardLike = 0;
						_self.activeCardDisLike = 0;

					}

					//_self.$store.commit('changeTop', activeId);

				}
			});

		},
		methods: {
						deleteLikesCloth(productNum, index) {
			
							this.$store.state.likeCloths.splice(index, 1)
						},
						deleteDisLikesCloth(productNum, index) {
							this.$store.state.dislikeCloths.splice(index, 1)
			
						}
			
		},
		watch: {
			
		}

	}
</script>

<style scoped src="../../../static/css/swiper/swiper.min.css"></style>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.pagebox {
		height: 65%;
		overflow: auto;
	}
	
	.likeBox,
	.dislikeBox {
		overflow: hidden;
		margin-bottom: 20px;
	}
	
	.title {
		text-align: left;
		margin-bottom: 10px;
	}
	
	.question-subject {
		margin-bottom: 15px;
	}
	.likecloth {
		width: 32.2%;
		margin-right: 1%;
		float: left;
		padding: 5px;
		margin-bottom: 5px;
		background: #FFF;
		box-sizing: border-box;
	}
	
	.addlike,
	.adddislike {
		float: right;
	}
	
	.addlike img,
	.adddislike img {
		display: inline-block;
		vertical-align: middle;
		margin-right: 10px;
		width: 20px;
	}
	
	.likeclothinner {
		background: #ccc;
		position: relative;
		height: 18vh;
		overflow: hidden;
	}
	
	.likeclothinner img {
		display: block;
		width: 100%;
	}
	
	.delete {
		width: 20px;
		position: absolute;
		top: 10px;
		right: 10px;
	}
	
	.delete img {
		width: 100%;
	}
	
</style>


