<template>
	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">2</span>请选择服装类型</p>
		
		<div class="swiper_pagination"></div>

		<img src="static/image/q2yj.png" class="yj" />
		<div class="swiper-container" id="parent-swiper-container">

			<div class="swiper-wrapper">
				<div class="swiper-slide">
					<div class="swiper-container" id="child-top-swiper-container">
						<div class="swiper-wrapper">
							<div class="swiper-slide" data-id="0"><img src="static/image/q2max.png" />
								<p>不限</p>
							</div>
							<div class="swiper-slide" data-id="1">
								<img src="static/image/q2nc.png" />
								<p>内搭</p>
							</div>
							<div class="swiper-slide" data-id="2">
								<img src="static/image/q2nw.png" />
								<p>内外可穿</p>
							</div>
							<div class="swiper-slide" data-id="3">
								<img src="static/image/q2wt.png" />
								<p>外搭</p>
							</div>
						</div>
					</div>
				</div>
				<div class="swiper-slide">
					<div class="swiper-container" id="child-center-swiper-container">
						<div class="swiper-wrapper">
							<div class="swiper-slide" data-id="0"><img src="static/image/q2max.png" />
								<p>不限</p>
							</div>
							<div class="swiper-slide" data-id="1"><img src="static/image/q2kz.png" />
								<p>裤装</p>
							</div>
							<div class="swiper-slide" data-id="2"><img src="static/image/q2qz.png" />
								<p>半裙</p>

							</div>

						</div>
					</div>
				</div>
				<div class="swiper-slide">
					<div class="swiper-container" id="child-bottom-swiper-container">
						<div class="swiper-wrapper">
							<div class="swiper-slide" data-id="0"><img src="static/image/q2max.png" />
								<p>不限</p>
							</div>
							<div class="swiper-slide" data-id="1"><img src="static/image/q2lyq.png" />
								<p>连衣裙</p>
							</div>
							<div class="swiper-slide" data-id="2"><img src="static/image/q2ltk.png" />
								<p>连体裤</p>
							</div>
							<div class="swiper-slide" data-id="3"><img src="static/image/q2fy.png" />
								<p>风衣</p>
							</div>
							<div class="swiper-slide" data-id="4"><img src="static/image/q2dy.png" />
								<p>大衣</p>
							</div>
							<div class="swiper-slide" data-id="5"><img src="static/image/q2tz.png" />
								<p>套装</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<a class="next-subject" href="#/appointmentSelf/question3">NEXT</a>
	</div>
</template>

<script>
	import Swiper from '../../../static/script/swiper/swiper.min.js'

	export default {
		computed: {
			Top() {

				return this.$store.state.Top
			},
			Bottom() {
				return this.$store.state.Bottom
			},
			Jumpsuit() {
				return this.$store.state.Jumpsuit
			},
		},
		data() {
			return {
				swiperParent: new Object(),
				swiperTop: new Object(),
				swiperJumpsuit: new Object(),
				swiperBottom: new Object()

			}
		},
		mounted: function() {
			var _self = this;
			this.swiperParent = new Swiper('#parent-swiper-container', {
				pagination: '.swiper_pagination',
				paginationType: 'custom', //自定义-分页器样式类型（前提）
				//设置自定义分页器的内容
				paginationCustomRender: function(swiper, current, total) {
					var _html = '';
					var paginatins = ['上装', '下装', '连体']
					for(var i = 0; i < paginatins.length; i++) {
						if(current == (i + 1)) {
							_html += '<li class="swiper-pagination-custom active">' + paginatins[i] + '</li>';
						} else {
							_html += '<li class="swiper-pagination-custom">' + paginatins[i] + '</li>';
						}
					}
					return _html; //返回所有的页码html
				},
				effect: 'coverflow',
				direction: 'vertical',
				centeredSlides: true,
				coverflow: {
					rotate: 50,
					stretch: 0,
					depth: 100,
					modifier: 1,
					slideShadows: true
				}
			});
			this.swiperTop = new Swiper('#child-top-swiper-container', {
				effect: 'coverflow',
				centeredSlides: true,
				slidesPerView: 2,
				initialSlide: this.Top,
				onTransitionStart: function(swiper) {
					var activeId = swiper.slides[swiper.activeIndex].dataset.id; //获取data-id
					_self.$store.commit('changeTop', activeId);

				}

			});
			this.swiperJumpsuit = new Swiper('#child-center-swiper-container', {
				effect: 'coverflow',
				centeredSlides: true,
				slidesPerView: 2,
				initialSlide: this.Jumpsuit,
				onTransitionStart: function(swiper) {
					var activeId = swiper.slides[swiper.activeIndex].dataset.id; //获取data-id
					_self.$store.commit('changeJumpsuit', activeId);
				}
			});
			this.swiperBottom = new Swiper('#child-bottom-swiper-container', {
				effect: 'coverflow',
				centeredSlides: true,
				slidesPerView: 2,
				onTransitionStart: function(swiper) {
					var activeId = swiper.slides[swiper.activeIndex].dataset.id; //获取data-id
					_self.$store.commit('changeBottom', activeId);
				}
			});
		}
	}
</script>

<style src="../../../static/css/swiper/swiper.min.css"></style>
<style>
	.pagebox .swiper_pagination {
		position: absolute;
		top: 20%;
		bottom: inherit;
		left: 10%;
		width: 80%;
	}
	
	.pagebox .swiper_pagination .swiper-pagination-custom {
		float: left;
		width: 33.3%;
	}
	
	.pagebox .swiper-pagination-custom.active {
		color: #f05500;
	}
</style>
<style scoped>
	#parent-swiper-container {
		margin-top: 20%;
	}
	
	.swiper-slide:before {
		content: "";
		line-height: 30vh;
	}
	
	.yj {
		width: 25%;
		position: absolute;
		top: 25%;
		left: 37.5%;
	}
	
	.swiper-container {
		height: 50vh;
	}
	
	.swiper-slide p {
		margin-top: 10px;
	}
	
	.swiper-slide-active>p {
		color: #f05500
	}
	
	.swiper-slide img {
		display: inline-block;
		vertical-align: middle;
		width: 50%;
		margin: 0 auto;
	}
	
	.pagebox {
		padding: 30px;
	}
	
	.question-subject {
		margin-bottom: 20px;
		text-align: left;
	}
	
	.next-subject {
		position: fixed;
		bottom: 30px;
		width: 33.3%;
		left: 33.3%;
		border: 1px solid #FFF;
		border-radius: 5px;
		padding: 10px 0;
		text-align: center;
	}
	
	.questionNum {
		color: #000;
		background: #FFF;
		width: 25px;
		height: 25px;
		display: inline-block;
		vertical-align: middle;
		border-radius: 50%;
		line-height: 25px;
		text-align: center;
		margin-right: 15px;
		font-size: 14px;
	}
</style>


