<template>
	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择面料</p>
		<div class="materialContainer">
			<div class="materials" v-for="(material,index) in materials">

				<div class="material" v-for="(omaterial,i) in material" :class="{ 'active': 1 == omaterial.checked }" @click="selectClothMaterial($event,omaterial.id,index,i)"><span>{{ omaterial.materialName }}</span></div>
			</div>
		</div>
		<a class="next-subject" href="#/appointmentSelf/question9">NEXT</a>
	</div>
</template>
<script>
	export default {
		name: 'question8',
		data() {
			return {
				num: 8,
				selectMaterialsIds: [],
				materials: [
					[{
						materialName: '羊毛',
						id: 5,
						checked: 0
					}],
					[{
							materialName: '棉',
							id: 1,
							checked: 0
						},
						{
							materialName: '亚麻',
							id: 4,
							checked: 0
						}
					],
					[{
							materialName: '麻',
							id: 2,
							checked: 0
						},
						{
							materialName: '不限',
							id: 0,
							checked: 0,
							all: true
						},

						{
							materialName: '羊绒',
							id: 6,
							checked: 0
						}
					],
					[{
							materialName: '化纤',
							id: 7,
							checked: 0
						},
						{
							materialName: '真丝',
							id: 3,
							checked: 0
						}
					],
					[{
						materialName: '新高性能纤维',
						id: 8,
						checked: 0
					}],
					[{
						materialName: '皮草',
						id: 9,
						checked: 0
					}],
				]
			}

		},
		created: function() {
			for(var i = 0; i < this.materials.length; i++) {
				for(var j = 0; j < this.materials[i].length; j++) {
					for(var k = 0; k < this.SroreClothMaterial.length; k++) {
						if(this.SroreClothMaterial[k] == this.materials[i][j].id) {
							this.materials[i][j].checked = 1;
						}
					}
				}
			}
		},
		computed: {
			//获取全局布料Id
			SroreClothMaterial() {
				return this.$store.state.materialId;
			},
			selectunAll() {
				//计算全选状态不限
				for(var i = 0; i < this.materials.length; i++) {
					for(var j = 0; j < this.materials[i].length; j++) {
						if(this.materials[i][j].checked == 1 && !this.materials[i][j].all) {

							return 0;
						}
					}
				}
				return 1;
			}

		},
		methods: {
			selectClothMaterial(ev, id, index, i) {
				if(this.materials[index][i].all) {
					//点击不限取消其他选中状态

					for(var k = 0; k < this.materials.length; k++) {
						for(var j = 0; j < this.materials[k].length; j++) {
							this.materials[k][j].checked = 0
						}
					}
				}
				this.materials[index][i].checked = !this.materials[index][i].checked;
				this.materials[2][1].checked = this.selectunAll;
				this.getMaterialIdArr()
			},
			getMaterialIdArr() {
				var selectMaterialsIds = [];
				for(var i = 0; i < this.materials.length; i++) {
					for(var j = 0; j < this.materials[i].length; j++) {
						if(this.materials[i][j].checked == 1) {
							selectMaterialsIds.push(this.materials[i][j].id)
						}
					}

				}

				this.$store.commit('changeMaterial', selectMaterialsIds);
			}
		}
	}
</script>

<style scoped>
	.pagebox {
		padding: 10%;
	}
	.materialContainer{
		position: relative;
	}
	.materials {
		overflow: hidden;
		line-height: 3.5em;
	}
	
	.materials:nth-child(1),
	.materials:nth-child(5) {
		margin-right: .5rem;
	}
	
	.materials:nth-child(1) .material,
	.materials:nth-child(5) .material {
		margin: 0 auto;
		float: none;
	}
	
	.materials:nth-child(2),
	.materials:nth-child(4) {
		width: 8.5em;
		margin-left: auto;
		margin-right: auto;
	}
	
	.materials:nth-child(2) {
		transform: translateY(-.5em);
	}
	
	.materials:nth-child(3) {
		transform: translateY(-1em);
		width: 12.5em;
		margin-left: auto;
		margin-right: auto;
	}
	
	.materials:nth-child(4) {
		transform: translateY(-1.5em);
	}
	
	.materials:nth-child(5) {
		transform: translateY(-2em);
	}
	
	.materials:nth-child(5) .material {
		margin: 0 auto;
		float: none;
		line-height: 1.5em;
		padding: 2em 1em;
		box-sizing: border-box;
	}
	
	.materials:last-child {
		position: absolute;
		bottom: 10px;
		right: 10%;
		
	}
	
	.materials:last-child .material {
		float: right;
	}
	
	.material:nth-child(2) {
		margin-left: .5em;
		margin-right: .5em;
	}
	
	.material {
		transition: .35s all;
		width: 6em;
		font-size: .65em;
		height: 6.6em;
		padding: 1em;
		box-sizing: border-box;
		float: left;
		background-image: url(../../../static/image/q8x.png);
		background-size: cover;
		background-repeat: no-repeat;
	}
	
	.material.active {
		background-image: url(../../../static/image/q8xs.png);
	}
</style>


