	position: fixed;
	bottom: 0;
}

.yuyueFt li {
	float: left;
	text-align: center;
	color: #fff;
}

.yuyueFt .yuyueCl {
	width: 25%;
	float: left;
	font-size: 0.26rem;
}

.totle {
	float: left;
	width: 50%;
	font-size: 0.36rem;
}
.clearLocalst{
	width: 100%;
	height: 0.7rem;
	line-height: 0.7rem;
	font-size: 0.3rem;
	color: #fff;
	background: rgba(158,158,158,0.4);
	text-align: center;
	position: absolute;
	bottom: 1.6rem;
}
/*选择颜色和尺码*/
.selectColorSize{
	width: 100%;
	height: 56%;
	max-height: 370px;
	position: absolute;
	bottom: -800px;
	left: 0;
	display: none;
	background:url(../../static/image/yuyue/bg@2x.png) no-repeat;
	background-size: 100% 100%;
	z-index: 999;;
}
.imginfo{
	
     justify-content: center;
    display: flex;
    text-align: center;
    position: absolute;
    top: 2%;
    left: 50%;
    transform: translate( -50%, 0);
    width: 1.7rem;
    height: 1.7rem;
    border-radius: 50%;
    background: #fff;
    align-items: center;
    overflow: hidden;
}
.activeImg{
	margin:0 auto;
	height: 100%;
	width: 60%;
}
.TagPrice{
	display: flex;
    position: absolute;
    top: 19%;
    left: 18px;
    font-size: 14px;
    font-weight: 500;
    color: #000;
    
}
#select_size,#select_color{
	margin-left:3px;
}
.colorSize{
	display: flex;
    position: absolute;
    top: 25%;
    left: 18px;
    font-size: 14px;
    font-weight: 500;
    color: #aba8a8;
}
.allcolorsize{
	position: absolute;
	width:100%;
	height: 40%;
	display: flex;
	justify-content: center;
	top:34%;
	left:0;
	flex-wrap: wrap;
}
.allcolor,.allsize{
	display: flex;
	justify-content: center;
    height: 50px;
    width: 100%;
    align-items: center;
}
.allsize div{
	display: none;
	font-size: 0.6rem;
}
.allsize span{margin:0 .25rem;}
.allcolor img{
	width: 40px;
    margin: 0 8px;
    border-radius: 50%;
    height: 40px;
}
.active_color{
	border:1px solid #222;
}
.active_size{
	border:1px solid #222;
}
.confrim{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 1rem;
	border-top: 1px solid #e0ddd0;
	position: absolute;
	left:0;
	bottom: 0;
}
#confrim{
    font-size: .3rem;
    font-weight: 400;
    letter-spacing: 10px;
}
.closebtn{
    position: absolute;
    right: 5%;
    top: 20%;
    width: .55rem;
    height: .55rem;
}
.mask{
	width:100%;
	height: 100%;
	position: absolute;
	top:0;left:0;
	z-index: 998;
	display: none;
	background: rgba(0,0,0,0.6);
}
.psdBody{
	width: 100%;
    height: 100%;
    background: #9d8c6f;
    background: url(../../static/image/public/bg@2x.png) no-repeat;
    background-size: cover;
    overflow: hidden;
}
.psdBox{
	width: 5.25rem;
    height: 4.56rem;
    position: relative;
    margin: 0.5rem auto 0;
}
.psdBox input{
	width: 100%;
	height:1.14rem ;
	line-height:1.14rem ;
	background: transparent;
	font-size: 0.3rem;
	display: inherit;
	text-indent: 0.2rem;
	border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.getCode{
	font-size: 0.18rem;
    color: #fff;
    padding: 0.1rem;
    border-radius: 5px;
    position: absolute;
    background: rgb(232,91,91);
    right: 0.1rem;
    top: 1.5rem;
}
.sureLogin{
	width: 5.25rem;
    height: 0.82rem;
    line-height: 0.82rem;
    background: rgb(232,91,91);
    border-radius: 5px;
    margin: 0.78rem auto 0.2rem;
    display: block;
    font-size: 0.34rem;
    text-align: center;
    color: #fff;
}


</style>


