<template>
	<div class="yichu">
		<div class='yinDaoy'>
			<div class="know"></div>
		</div>
		<div class="yichuClo">
			<!--头像和搜索栏-->
			<div class="header">
				<div class="hedIpBox">
					<input type="text" v-model="serachkey" class="huohao" placeholder="输入您要查找的内容" /><img class="searchMsg search" v-on:click="searchProduct()" src="static/image/public/ss@2x.png" alt="" />
				</div>
				<div class="imgBg" v-on:click="showSearch()">
					<img src="static/image/public/ss@2x.png" alt="" />
				</div>
			</div>

			<!--买手商城-->
			<div id="buyClothes">
				<div class="minddleBox">
					<!--logo 标题-->
					<div class="logo_buyahand">
						<img src="static/image/public/logo@2x.png" alt="" />商城
					</div>
					<!--买手区域-->
					<div class="buyers">
						<!--左边 衣服-->
						<div class="cloDec clothes">
							<div data-type="1" v-on:click="openProduct(1)">
								<img class="top_cloth" src="static/image/buyhand/jacket2@2x.png" alt="" />
								<span>上装</span>
							</div>
							<div data-type="2" v-on:click="openProduct(2)">
								<img class="top_cloth" src="static/image/buyhand/dress@2x.png" />
								<span>下装</span>
							</div>
							<div data-type="3" v-on:click="openProduct(3)">
								<img class="bottom_cloth" src="static/image/buyhand/dress4@2x.png" />
								<span>连体</span>
							</div>
						</div>
						<!--右边鞋子 装饰品-->
						<div class="cloDec decoration">
							<div data-type="4" v-on:click="openProduct(4)">
								<img src="static/image/buyhand/scarf@2x.png" />
								<span>围巾</span>
							</div>
							<div data-type="5" v-on:click="openProduct(5)">
								<img src="static/image/buyhand/bag44@2x.png" />
								<span>包</span>
							</div>
							<div data-type="6" v-on:click="openProduct(6)">
								<img src="static/image/buyhand/diamond22@2x.png" />
								<span>饰品</span>
							</div>
							<div data-type="7" v-on:click="openProduct(7)">
								<img src="static/image/buyhand/feminine3@2x.png" />
								<span>鞋</span>
							</div>
						</div>
					</div>
				</div>
			</div>

		</div>

	</div>
</template>

<script>
	import $ from '../../../static/script/jquery/jquery-2.1.1.min.js'

	export default {
		data() {
			return {
				serachkey:''
			}
		},
		methods: {
			showSearch() {
				$('.imgBg').hide()
				$(".hedIpBox").animate({
					top: '0'
				});
			},
			searchProduct() {
				window.localStorage.setItem("val", this.serachkey);
				window.localStorage.setItem("datt", 1);
				
				location.href = this.$store.state.productDetial;
			},
			openProduct(id){
				window.localStorage.setItem("ParentCategoryName", id);
				window.localStorage.setItem("datt",0);
				location.href = this.$store.state.productDetial;
			}
			
		}
	}
</script>

<style scoped>
	/*搜索*/
	
	.header {
		width: 100%;
		top: 0;
		text-align: center;
		position: absolute;
		z-index: 100;
	}
	
	.hedIpBox {
		position: absolute;
		top: -13.8vw;
		left: 0;
		height: 14vw;
		width: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.header .imgBg {
		width: 12vw;
		height: 6.4vw;
		position: absolute;
		background: rgba(0, 0, 0, 0.5);
		border-radius: 0 0 6.4vw 6.4vw;
		left: 50%;
		margin-left: -6vw;
	}
	
	.header .search {
		width: 5vw;
		position: absolute;
		right: 5vw;
		top: 5vw;
	}
	
	.imgBg img {
		width: 30%;
		position: absolute;
		margin-left: -2vw;
	}
	
	.header input {
		width: 80%;
		height: 10vw;
		line-height: 10vw;
		background: transparent;
		border: none;
		/*text-align: center;*/
		position: absolute;
		left: 10%;
		color: #fff;
		top: 1vh;
		outline: none;
	}
	/*买手商城*/
	/*左右公共区域 底部边框*/
	
	.clothes div:not(:last-child),
	.decoration div:not(:last-child) {
		border-bottom: 1px solid rgba(255, 255, 255, 0.3);
	}
	
	.clothes div span,
	.decoration div span {
		color: rgba(255, 255, 255, 0.7);
		display: block;
	}
	/*图片剧中显示的公共 i 属性*/
	/*.buyers  i{
	height: 100%;
	display: inline-block;
	vertical-align: middle;
}	*/
	
	.clothes,
	.buyers .decoration {
		text-align: center;
	}
	/*logo 标题*/
	
	.logo_buyahand {
		width: 100%;
		height: 6vh;
		line-height: 6vh;
		text-align: center;
		color: rgba(255, 255, 255, 0.8);
		border-bottom: 1px solid rgba(255, 255, 255, 0.3);
	}
	
	.logo_buyahand img {
		width: 5vw;
		margin-right: 10px;
		vertical-align: middle;
	}
	
	.bagfil {
		width: 100%;
		overflow: hidden;
	}
	
	.bgfilter {
		-webkit-filter: blur(3px);
		-moz-filter: blur(3px);
		-o-filter: blur(3px);
		-ms-filter: blur(3px);
		filter: blur(3px);
	}
	
	#buyerBox {
		width: 100%;
	}
	
	.warmPrompt {
		position: absolute;
		background: rgba(0, 0, 0, 1);
		font-size: 0.26rem;
		color: #fff;
		bottom: 1rem;
		left: 0.32rem;
		padding: 5px;
		border-radius: 5px;
		display: none;
	}
	
	.showHid {
		top: 130%;
		position: absolute;
		z-index: 10;
		display: none;
	}
	
	#buyClothes {
		padding-top: 10vh;
	}
	
	.hand_mall,
	.minddleBox {
		width: 80vw;
		height: 70vh;
		/*position: absolute;*/
		background-color: rgba(0, 0, 0, 0.5);
		margin: 0 auto;
		overflow: hidden;
	}
	/*左边区域*/
	
	.buyers {
		width: 100%;
		height: 64vh;
	}
	
	.clothes {
		width: 50%;
	}
	
	.decoration {
		width: 50%;
	}
	
	.clothes,
	.decoration {
		height: 100%;
		float: left;
		box-sizing: border-box;
	}
	
	.clothes {
		border-right: 1px solid rgba(255, 255, 255, 0.3);
	}
	
	.clothes>div {
		width: 100%;
		height: 33.3%;
	}
	
	.clothes div img {
		width: 30%;
		vertical-align: middle;
	}
	
	.clothes div .top_cloth {
		margin-top: 3vh;
	}
	
	.bottom_cloth {
		margin-top: 1vh;
	}
	/*右边区域*/
	
	.decoration>div {
		width: 100%;
		height: 25%;
	}
	
	.decoration div img {
		margin-top: 3vh;
		width: 25%;
		vertical-align: middle;
	}
</style>


