<template>
	<div class="pagebox">

		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择喜欢的颜色</p>
		<div class="colorContainer">
			<div class="color" :class="{'active':color.checked == 1}" v-for="(color,index) in colorList" @click="clickColor($event,index,color.id)" :style="{'background-color':color.color,'background-image':'url('+ color.src+')'}"></div>
		</div>
		<a class="next-subject" href="#/appointmentSelf/question8">NEXT</a>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				num: 7,
				likeColor: [],
				colorList: [{
						name: '黑白灰',
						id: 1,
						src: './static/image/q7bwg.png',
						checked: 0
					},
					{
						name: '红色',
						color: '#D30002',
						id: 2,
						checked: 0
					},
					{
						name: '绿色',
						color: '#2DE8CE',
						id: 3,
						checked: 0
					},
					{
						name: '黄色',
						color: '#21D103',
						id: 4,
						checked: 0
					},
					{
						name: '蓝色',
						color: '#119FCF',
						id: 5,
						checked: 0
					},
					{
						name: '紫色',
						color: '#9904E2',
						id: 6,
						checked: 0
					},
					{
						name: '橙色',
						color: '#ED9E03',
						id: 7,
						checked: 0
					},
					{
						name: '条纹',
						src: './static/image/q7tw.png',
						id: 8,
						checked: 0
					},
					{
						name: '格子',
						src: './static/image/q7gz.png',
						id: 9,
						checked: 0
					},
					{
						name: '马卡龙色',
						color: '#D50D76',
						id: 10,
						checked: 0
					}
				]
			}
		},
		created: function() {
			var likeColor = this.SroreLikeColor;
			for(var i = 0; i < this.colorList.length; i++) {
				for(var j = 0; j < likeColor.length; j++) {
					if(this.colorList[i].id == likeColor[j]) {
						this.colorList[i].checked = 1;
					}
				}
			}
		},
		computed: {
			//获取全局季节Id
			SroreLikeColor() {
				return this.$store.state.likeColor
			}
		},
		methods: {
			clickColor(ev, index, id) {
				this.colorList[index].checked = !this.colorList[index].checked;
				this.selectLikeColor()
			},
			selectLikeColor() {
				this.likeColor = [];
				for(var i = 0; i < this.colorList.length; i++) {
					if(this.colorList[i].checked) {
						this.likeColor.push(this.colorList[i].id)
					}
				}
				if(this.likeColor.length <= 0) {
					this.likeColor = [0]; //默认不限
				}
				this.$store.commit('changeColor', this.likeColor);

			}

		}
	}
</script>

<style scoped>
	.color {
		width: 50px;
		height: 50px;
		margin: 10px;
		border-radius: 50%;
		float: left;
		background-size: 100%;
		transition: .35s all;
	}
	
	.color.active {
		transform: scale(1.2, 1.2);
		box-shadow: 0 0 5px rgba(255, 255, 255, .5), 0 0 3px rgba(0, 0, 0, .85);
	}
</style>


