import Vue from 'vue'
import Router from 'vue-router'


//login
import Login from '@/views/login'

//home index
import Index from '@/views/index'

//home page
import HomeAppointment from '@/views/home/homeAppointment'
import HomeMall from '@/views/home/homeMall'
import HomeTest from '@/views/home/homeTest'
import HomeMine from '@/views/home/homeMine'

//自助预约
import AppointmentAnswer from '@/views/appointment/appointmentAnswer'
import Question1 from '@/views/appointment/question1'
import Question2 from '@/views/appointment/question2'
import Question3 from '@/views/appointment/question3'
import Question4 from '@/views/appointment/question4'
import Question5 from '@/views/appointment/question5'
import Question6 from '@/views/appointment/question6'
import Question7 from '@/views/appointment/question7'
import Question8 from '@/views/appointment/question8'
import Question9 from '@/views/appointment/question9'
import Question10 from '@/views/appointment/question10'
import Product from '@/views/appointment/product'
import DisProduct from '@/views/appointment/disproduct'

Vue.use(Router)

export default new Router({
	routes: [
		//登录
		{
			path: '/login',
			name: 'login',
			component: Login
		},
		//home index
		{
			path: '/',
			name: 'index',
			component: Index,
			children: [{
					path: '/',
					name: 'homeAppointment',
					component: HomeAppointment
				},
				{
					path: '/homeMall',
					name: 'homeMall',
					component: HomeMall
				},
//				{
//					path: '/homeTest',
//					name: 'homeTest',
//					component: HomeTest
//				},
				{
					path: '/homeMine',
					name: 'homeMine',
					component: HomeMine
				},

			]
		},
		{
			path: '/appointmentSelf',
			name: 'appointmentSelf',
			component: AppointmentAnswer,
			children: [{
					path: '/appointmentSelf/question1',
					name: 'Question_1',
					component: Question1,

				},
				{
					path: '/appointmentSelf/question2',
					name: 'Question_2',
					component: Question2,

				},
				{
					path: '/appointmentSelf/question3',
					name: 'Question_3',
					component: Question3
				},
				{
					path: '/appointmentSelf/question4',
					name: 'Question_4',
					component: Question4
				},
				{
					path: '/appointmentSelf/question5',
					name: 'Question_5',
					component: Question5
				},
				{
					path: '/appointmentSelf/question6',
					name: 'Question_6',
					component: Question6
				},
				{
					path: '/appointmentSelf/question7',
					name: 'Question_7',
					component: Question7
				},
				{
					path: '/appointmentSelf/question8',
					name: 'Question_8',
					component: Question8
				},
				{
					path: '/appointmentSelf/question9',
					name: 'Question_9',
					component: Question9
				},
				{
					path: '/appointmentSelf/question10',
					name: 'Question_10',
					component: Question10
				},
				{
					path: '/appointmentSelf/product',
					name: 'Product',
					component: Product
				},
				{
					path: '/appointmentSelf/disproduct',
					name: 'DisProduct',
					component: DisProduct
				}
			]

		}

	]
})


