<template>

	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择金额总预算</p>
		<div class="unit">
			<p>单位：元</p>
		</div>
		<div class="budgutContainer" v-bind:class="budgetColor">
			<div class="price" v-bind:class="{'active':price.checked == 1}" v-for="(price,index) in budGet" v-on:click="clickPrice(index,price.id,$event)">
				<div class="priceinner"><span>{{ price.des }}</span></div>
			</div>
		</div>
		<a class="next-subject" href="#/appointmentSelf/question5">NEXT</a>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				num: 4,
				budGet: [{
						id: 1,
						des: '1000以内',
						checked: 0,
					},
					{
						id: 2,
						des: '1000-2000',
						checked: 0,

					},
					{
						id: 3,
						des: '2000-5000',
						checked: 0,

					},
					{
						id: 4,
						des: '5000以上',
						checked: 0,

					}
				]
			}
		},
		computed: {
			budgetColor() {
				var budgetColorClassSurname = 'budgetColorClass';
				var budgetColorClassName = budgetColorClassSurname + this.SrorebudGetColor;
				for(var i = 0; i < this.budGet.length; i++) {

					if(this.budGet[i].checked == 1) {
						var budgetColorClassName = budgetColorClassSurname + this.budGet[i].id;
					};
				}
				return budgetColorClassName;
			},
			//获取全局预算Id
			SrorebudGetColor() {
				return this.$store.state.budGet;
			}
		},
		methods: {
			clickPrice(index, id, thisEv) {
				var deg = this.budGet[index].transformRotateZ; //*90;

				for(var i = 0; i < this.budGet.length; i++) {					
					this.budGet[i].checked = 0;
				}
				this.budGet[index].checked = !this.budGet[index].checked;
				

				this.selectBudGet(id)
			},
			selectBudGet(id) {
				this.$store.commit('changeBudGet', id);
			}
		}
	}
</script>

<style scoped>
	.unit {
		position: absolute;
		top: 65px;
		margin-top: 37%;
		left: 40%;
		width: 20%;
		width: 20%;
		padding-top: 20%;
		border: 1px solid rgba(255, 255, 255, .5);
		transform: rotateZ(45deg);
	}
	
	.unit p {
		transform: rotateZ(-45deg);
		position: absolute;
		top: 38%;
		left: 0%;
		width: 100%;
		font-size: 12px;
	}
	
	.pagebox {
		padding: 0;
		margin: 30px;
		position: relative;
	}
	
	.budgutContainer {
		position: absolute;
		top: 65px;
		left: 10%;
		margin-left: -20px;
		width: 80%;
		padding-top: 80%;
		border-top: 20px solid rgba(255, 255, 255, .3);
		border-bottom: 20px solid rgba(255, 255, 255, .3);
		border-left: 20px solid rgba(255, 255, 255, .5);
		border-right: 20px solid rgba(255, 255, 255, .5);
		border-radius: 50%;
		transition: 2s all;
	}
	
	.budgetColorClass1 {
		border-top: 20px solid transparent;
		transform: rotateZ(180deg);
	}
	.budgetColorClass1 .price{
		transform: rotateZ(-180deg);
		
	}
	.budgetColorClass2 {
		border-right: 20px solid transparent;
		transform: rotateZ(90deg);
		
	}
	.budgetColorClass2 .price{
		transform: rotateZ(-90deg);
		
	}
	.budgetColorClass3 {
		border-left: 20px solid transparent;
		transform: rotateZ(-90deg);
		
	}
	.budgetColorClass3 .price{
		transform: rotateZ(90deg);
		
	}
	.budgetColorClass4 {
		border-bottom: 20px solid transparent;
		transform: rotateZ(0deg);
		
	}
	.budgetColorClass4 .price{
		transform: rotateZ(0deg);
		
	}
	.price.active {
		color: #FFF;
		border: 3px solid #e8e026;
	}
	
	.price.active .priceinner {
		background: none;
	}
	
	.price .priceinner span {
		padding: 5px;
		font-size: 14px;
		display: table-cell;
		vertical-align: middle;
	}
	
	.price {
		width: 40%;
		height: 40%;
		border: 5px solid rgba(255, 255, 255, .2);
		box-sizing: border-box;
		color: #000;
		overflow: hidden;
		border-radius: 50%;
		position: absolute;
	}
	
	.priceinner {
		transition: 2s background-color;
	}
	
	.price .priceinner {
		display: table;
		padding: 10px;
		background: #e8e026;
		height: 104%;
		margin-top: -2%;
		width: 100%;
		margin-left: -10px;
	}
	
	.price:nth-child(1) {
		position: absolute;
		top: -20%;
		left: 30%;
	}
	
	.price:nth-child(2) {
		position: absolute;
		top: 30%;
		right: -20%;
	}
	
	.price:nth-child(3) {
		position: absolute;
		top: 30%;
		left: -20%;
	}
	
	.price:nth-child(4) {
		position: absolute;
		bottom: -20%;
		left: 30%;
	}
</style>


