<template>
	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择季节</p>
		<ul class="seasons">
			<li :class="{active:SroreClothSeason == season.id }" :seasonId="season.id" v-for="(season,inedx) in seasons" @click="selectSeason($event,season.id)">
				<div v-if="inedx==0" class="season-animate">
					<img class="branch" src="static/image/q9Branch.png" />
					<img class="Leaf" src="static/image/q9Leafone.png" />
					<img class="Leaf" src="static/image/q9Leafth.png" />
					<img class="Leaf" src="static/image/q9Leaftwo.png" />
				</div>
				<div v-if="inedx==1" class="season-animate">
					<img class="flower-1" src="static/image/q9flower.png" />
					<img class="flower-2" src="static/image/q9flowerh.png" />
					<img class="flower-3" src="static/image/q9flowersy.png" />
					<img class="rain" v-for="i in 10" src="static/image/q9rain.png" />
				</div>
				<div v-if="inedx==2" class="season-animate">
					
					<img class="snowpeople" src="static/image/q9snowpeople.png" />
					<img class="snowflower" v-for="i in 7" src="static/image/q9snow.png" />					
				</div>
				<div v-if="inedx==3" class="season-animate">
					<img class="mt" src="static/image/q9mt.png" />
					<img class="sun" src="static/image/q9sun.png" />
					<img class="cloud" src="static/image/q9cloud.png" />
					<img class="cloud" src="static/image/q9cloud.png" />
					
				</div>
				<div class="season-inner"><span class="season-name">{{ season.seasonName }}</span></div>
			</li>
		</ul>
		
		<a class="next-subject" href="#/appointmentSelf/question10">NEXT</a>
	</div>
</template>
<script>
	export default {
		name: 'question9',
		data() {
			return {
				num: 9,
				seasons: [{
						seasonName: '春秋',
						id: 1
					},
					{
						seasonName: '夏',
						id: 2
					},
					{
						seasonName: '冬',
						id: 3
					},
					{
						seasonName: '四季',
						id: 4
					}
				]
			}
		},
		computed: {
			//获取全局季节Id
			SroreClothSeason() {
				return this.$store.state.seasonId
			}
		},
		methods: {
			selectSeason(ev, id) {
				this.$store.commit('changeSeason', id);
			}
		}
	}
</script>


<style scoped>
	
	.seasons {
		height: 60vh;
		width: 100%;
	}
	
	.seasons li {
		position: relative;
		width: 25%;
		height: 100%;
		padding: 5px;
		list-style: none;
		float: left;
		box-sizing: border-box;
	}
	
	.season-inner {
		color: #CCC;
		background: rgba(255, 255, 255, .2);
		height: 100%;
		width: 100%;
		box-sizing: border-box;
		position: relative;
	}
	
	.season-name {
		width: 20px;
		font-size: 20px;
		position: absolute;
		top: 40%;
		left: 50%;
		margin-left: -10px;
	}
	
	.seasons li {
		opacity: .5;
	}
	
	.seasons li.active {
		opacity: 1;
	}
	
	.seasons li.active .season-inner {
		color: #a728e7;
		border-bottom: 3px solid #a728e7;
	}
	.seasons li.active .season-animate{
		display: block;
	}
	.season-animate {
		overflow: hidden;
		position: absolute;
		top: 5px;
		left: 5px;
		bottom: 5px;
		right: 5px;
		display: none;
	}
	
	.branch {
		width: 70%;
		float: right;
	}
	
	.Leaf {
		width: 20%;
		display: block;
		animation-duration: 4s;
		animation-iteration-count: infinite;
		animation-name: leaf;
	}
	
	.Leaf:nth-child(2) {
		position: absolute;
		top: 20px;
		left: 50%;
		animation-delay: 2s;
	}
	
	.Leaf:nth-child(3) {
		position: absolute;
		top: 30px;
		left: 30%;
		animation-delay:1s ;
	}
	
	.Leaf:nth-child(4) {
		position: absolute;
		top: 20%;
		right: 10%;
		animation-delay: .5s;
	}
	/*夏*/
	.flower-1 {
		width: 60%;
		position: absolute;
		bottom: 0;
		left: 15%;
	}
	
	.flower-2 {
		height: 18%;
		position: absolute;
		bottom: 0;
		left: 60%;
	}
	
	.flower-3 {
		position: absolute;
		width: 30%;
		left: 36%;
		bottom: 18%;
		transform-origin: bottom right;
		animation: 3s flower 1;
		animation-fill-mode: forwards;
	}
	
	.rain {
		width: 20%;
		top: -100px;
		position: absolute;
		animation: 2s rain infinite;
	}
	
	.rain:nth-child(4) {
		left: 10%;
		animation-delay: .5s;
	}
	
	.rain:nth-child(5) {
		left: 40%;
		animation-delay: 1s;
	}
	
	.rain:nth-child(6) {
		left: 70%;
		animation-delay: 1.3s;
	}
	
	.rain:nth-child(7) {
		left: 80%;
	}
	.snowpeople{
		width: 50%;
		position: absolute;
		left: 25%;
		bottom: 5px;
		transform-origin: bottom center;
		animation: 3s snowpeople infinite;
		animation-fill-mode: backwards;
	}
	.snowflower{
		width: 10%;
		position: absolute;
		animation: 5s snowflower infinite;
		top: -50px;
	}
	
	.snowflower:nth-child(2){
		left: 0;
	}
	.snowflower:nth-child(3){
		left: 15%;
		animation-delay: .5s;
	}
	.snowflower:nth-child(4){
		left: 50%;
		animation-delay: 1.2s;
	}
	.snowflower:nth-child(5){
		left: 75%;
		animation-delay: 1.5s;
	}
	.snowflower:nth-child(6){
		left: 80%;
		animation-delay: 2s;
	}
	.snowflower:nth-child(7){
		left: 5%;
		animation-delay: .7s;
	}
	.snowflower:nth-child(8){
		animation-delay: 1s;
		left: 90%;
	}
	/*四季*/
	.mt{
		width: 80%;
		position: absolute;
		bottom: 2px;
		left: 10%;
		z-index: 1000;
	}
	.sun{
		position: absolute;
		bottom: 0;
		left: 60%;
		width: 20%;
		animation: 3s sun 1;
		animation-fill-mode: forwards;
	}
	.cloud{
		width: 50%;
		position: absolute;
		top: 5%;
		left: -50%;
		animation: 3s cloud infinite;
	}
	.cloud:nth-child(3){
		top: 10%;
		animation-delay: 1s;
	}
	
	/*动画*/
	@keyframes leaf {
		0% {
			opacity: 0;
			transform: rotate(0deg) translate(0px, 0px);
		}
		99% {
			opacity: 1;
			transform: rotate(20deg) translate(10px, 300px);
		}
		100% {
			opacity: 0;
			transform: rotate(0deg) translate(0px, 0px);
		}
	}
	
	@keyframes mysnow {
		0% {
			bottom: 100%;
			opacity: 0;
		}
		50% {
			opacity: 1;
			transform: rotate(1080deg);
		}
		100% {
			transform: rotate(0deg);
			opacity: 0;
			bottom: 0;
		}
	}
	
	@keyframes flower {
		from {
			transform: rotateZ(0deg);
		}
		to {
			transform: rotateZ(-20deg);
		}
	}
	
	@keyframes rain {
		from {
			transform: translateY(-20px);
		}
		to {
			transform: translateY(400px);
			opacity: 0;
		}
	}
	@keyframes snowpeople{
		from{
		transform: rotateZ(-20deg);
		}
		50%{
			transform: rotateZ(20deg);
		}
		to{
		transform: rotateZ(-20deg);
		
			
		}
	}
	@keyframes snowflower{
		from{
			transform: rotateZ(0deg) ;
			top: -10px;
			opacity: 1;
		}
		to{
			transform: rotateZ(1800deg);
			top: 400px;
			opacity: 0;
		}
	}
	@keyframes sun{
		from{
			bottom: 0;
		}
		to{
			bottom: 10%;
			
		}
	}
	@keyframes cloud{
		from{
			transform: translateX(0px);
		}
		to{
			transform: translateX(200px);
			
		}
	}
</style>


