<template>
	<div class="index">
		<router-view></router-view>
		<footer id="footerMenu">
			<div class="menu" v-on:click="">
				<router-link to="/">
					<img src="static/image/index/yuyue.png" />
					<p>预约</p>
				</router-link>
			</div>
			<div class="menu">
				<router-link to="homemall">
					<img src="static/image/index/shangcheng.png" />
					<p>商城</p>
				</router-link>
			</div>
			<div class="menu">
				<!--<router-link to="hometest">-->
					<a href="static/store/fenggecs/fenggeceshi_report.html">
					<img src="static/image/index/ceshi.png" />
					<p>测试</p>
					</a>
				<!--</router-link>-->
			</div>
			<div class="menu">
				<router-link to="homemine">
					<img src="static/image/index/wode.png" />
					<p>我的</p>
				</router-link>
			</div>
		</footer>
	</div>
</template>

<script>
	export default {
		created: function() {
			var userMember = localStorage.getItem('MemberId');
			var u_num = localStorage.getItem('u_num');
			if(!userMember || !u_num) {
				window.location.href = this.$store.state.login;
			}
		}
	}
</script>

<style scoped>
	.index {
		height: 100%;
		background: url(../../static/image/index/loginbg.png);
		background-size: cover;
	}
	
	.index img {
		width: 100%;
		display: block;
	}
	
	#footerMenu {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 8vh;
		background: #000;
	}
	
	.router-link-exact-active {
		opacity: .3;
	}
	
	#footerMenu .menu {
		width: 25%;
		float: left;
	}
	
	#footerMenu .menu>a {
		display: block;
		width: 50%;
		margin: 0 auto;
	}
	
	#footerMenu .menu>a img {
		width: 40%;
		display: block;
		margin: 3px auto;
	}
	
	#footerMenu .menu>a p {
		color: #FFF;
		font-size: 12px;
		text-align: center;
	}
	
	.topimg_box,
	.bottomimg_box {
		position: absolute;
		width: 100%;
	}
	
	.topimg_box {
		top: 0;
		left: 0px;
	}
	
	.bottomimg_box {
		right: 0px;
		bottom: 8vh;
	}
	
	#zizhuyuyue {
		position: absolute;
		top: 20%;
		left: 15%;
		width: 30%;
	}
	
	#qingsongyuyue {
		position: absolute;
		bottom: 33%;
		right: 11%;
		width: 30%;
	}
	
	#zizhu_info {
		position: absolute;
		top: 20%;
		right: 15%;
		width: 10%;
	}
	
	#qingsong_info {
		position: absolute;
		bottom: 28%;
		left: 15%;
		width: 10%;
	}
	
	.bounceLeft {
		-webkit-animation: bounceLeft 1s;
	}
	
	.bounceRight {
		-webkit-animation: bounceRight 1s;
	}
	
	.bounceUp {
		-webkit-animation: bounceUp .85s forwards;
	}
	
	.bounceDown {
		-webkit-animation: bouncedDown .85s forwards;
	}
	
	@-webkit-keyframes bounceLeft {
		0% {
			-webkit-transform: translateX(-300px);
			-webkit-filter: brightness(.8);
		}
		50% {
			-webkit-filter: brightness(.3);
		}
		100% {
			-webkit-transform: translateX(0);
			-webkit-filter: brightness(1);
		}
	}
	
	@-webkit-keyframes bounceRight {
		0% {
			-webkit-transform: translateX(300px);
			-webkit-filter: brightness(.8);
		}
		50% {
			-webkit-filter: brightness(.3);
		}
		100% {
			-webkit-transform: translateX(0);
			-webkit-filter: brightness(1);
		}
	}
	
	@-webkit-keyframes bounceUp {
		0% {
			-webkit-transform: translateY(0);
		}
		100% {
			-webkit-transform: translateY(-500px);
		}
	}
	
	@-webkit-keyframes bouncedDown {
		0% {
			-webkit-transform: translateY(0);
		}
		100% {
			-webkit-transform: translateY(500px);
		}
	}
</style>


