<template>

	<div class="productContainer" v-on:touchend="scroll($event)">
		<p class="selectTitle">已选择{{SrorelikeCloths.length}}/5</p>
		<div class="bgBody">
			<div id="wrapper" class="cloBagd hideShop">
				<div id="scroller" class="appendBox">
					<div class="white_bgg" v-for="(item,index) in cloths" v-on:click="selectLikes(index,item.ProductNumber,item.ImagePath)" v-bind:class="{'selected':item.selected == 1}" v-bind:data-product-id="item.ProductId" v-bind:data-productnumber="item.ProductNumber">
						<div class="commodity">
							<img class="picture" v-bind:src="item.ImagePath">
							<div class="message_box">
								<div class="message">
									<p>￥{{item.Price }}</p>
									<ul class="mold">
										<li>{{ item.CategoryName }}</li>
										<li>{{ item.SeasonName }}</li>
									</ul>
								</div>
							</div>
						</div>
					</div>
					<div style="clear: both;"></div>
				</div>
			</div>

		</div>

		<div class="Shopping-Mall">
			<div class="shopping-mall-container">
				<div class="Shopping-Mall-title"><span>商城</span></div>
				<div class="mallclass mall-left-class">
					<div class="classification" data-type="1" v-on:click="changeClassification(1)"><img src="static/image/jacket2@2x.png" />
						<p>上装</p>
					</div>
					<div class="classification" data-type="2" v-on:click="changeClassification(2)"><img src="static/image/dress@2x.png" />
						<p>下装</p>
					</div>
					<div class="classification" data-type="3" v-on:click="changeClassification(3)"><img src="static/image/dress4@2x.png" />
						<p>连体</p>
					</div>

				</div>
				<div class="mallclass mall-right-class">
					<div class="classification" data-type="4" v-on:click="changeClassification(4)"><img src="static/image/scarf@2x.png" />
						<p>围巾</p>
					</div>
					<div class="classification" data-type="5" v-on:click="changeClassification(5)"><img src="static/image/bag44@2x.png" />
						<p>包</p>
					</div>
					<div class="classification" data-type="6" v-on:click="changeClassification(7)"><img src="static/image/diamond22@2x.png" />
						<p>饰品</p>
					</div>
					<div class="classification" data-type="7" v-on:click="changeClassification(6)"><img src="static/image/feminine3@2x.png" />
						<p>鞋</p>
					</div>

				</div>

			</div>

		</div>

		<!--底部导航栏-->
		<div class="footer_navigation">
			<ul>
				<li class="back_more">
					<a href="javascript:;" onclick="window.history.back();"><img src="data:image/png;base64," alt=""></a>
				</li>
				<li class="bottom_nav" v-on:click="showMenu()">
					<img src="static/image/sysp@2x.png" alt="" />
					<span>所有商品</span>
				</li>
				<li class="back_me"></li>
			</ul>
		</div>
	</div>
</template>

<script>
	import $ from '../../../static/script/jquery/jquery-2.1.1.min.js'
	
	
	var host = 'https://api.0-fashion.com',
		PageIndex = 1,
		pagdex = 1,
		CategoryId = 1,
		MemberId = window.localStorage.getItem('MemberId'),
		U_Num = window.localStorage.getItem('u_num'),
		maxIndexPage = 99999,
		PageSize = 10;
	var showMenu = false;

	export default {
		data() {
			return {
				cloths: [],

			}
		},
		created: function() {
			this.getCommodity()
		},
		computed: {
			//获取全局变量
			SrorelikeCloths() {
				return this.$store.state.likeCloths;
			}
		},
		methods: {
			getCommodity() {
				var _self = this;
				$('.dialog').remove()
				if(PageIndex > maxIndexPage) {
					return;
				}
				$.ajax({
					type: "post",
					url: host + "/api/Products/Product/GetListByStore",
					async: true,
					data: {
						MemberId: MemberId,
						StoreId: 14,
						CategoryId: CategoryId,
						PageIndex: PageIndex,
						PageSize: PageSize,
					},
					success: function(data) {
						var divFooter = '</div></div>',
							sedDivHed = '<div class="message_box"><div class="message">',
							sedDivFooter = '</div></div>';
						if(data.ResultType == '3') {
							var contacts = data.Data;
							maxIndexPage = data.PageCount;
							if(data.AllCount == 0) {

								var div = '<div class="dialog">还没有此类商品</div>';
								$(".appendBox").append(div);
							} else {
								for(var i = 0; i < contacts.length; i++) {
									//判断是否已选择
									if(_self.judgeSelected(contacts[i].ProductNumber)) {
										contacts[i].selected = 1;
									} else {
										contacts[i].selected = 0;

									}
									_self.cloths.push(contacts[i])
								}

							}

						}
					}
				});

			},
			judgeSelected(productNum) {
				var likescloths = this.SrorelikeCloths;
				for(var j = 0; j < likescloths.length; j++) {
					for(var item in likescloths[j]) {
						if(productNum == item) {
							return true;
						}
					}

				}
				return false;
			},
			scroll(e) {
				var scrollTop = $('.productContainer').scrollTop();
				var scrollHeight = $('#scroller').height();
				var offsetTop = scrollHeight - scrollTop - $(document).height()
				if(offsetTop < $(document).height()) {
					PageIndex++;
					this.getCommodity()
				}
			},
			selectLikes(index, productNum, imageSrc) {

				if(this.SrorelikeCloths.length >= 5 && this.cloths[index].selected == 0) {
					return;
				}

				this.cloths[index].selected = !this.cloths[index].selected;

				//同步全局变量
				if(this.cloths[index].selected) {
					var likecloth = {};
					likecloth[productNum] = imageSrc;
					this.SrorelikeCloths.push(likecloth)
				} else {
					for(var i = 0; i < this.SrorelikeCloths.length; i++) {
						if(this.SrorelikeCloths[i][productNum]) {
							this.SrorelikeCloths.splice(i, 1)
						}
					}
				}
			},
			changeClassification(id, index) {

				$('.Shopping-Mall').slideUp();
				$('.bgBody').removeClass('filter');
				CategoryId = id;
				PageIndex = 1;
				maxIndexPage = 99999;
				this.cloths.splice(0, this.cloths.length);

				this.getCommodity()
			},
			selectlikeCloths(ev, id) {

				this.$store.commit('changeLikeCloths', id);

			},
			showMenu() {
				if(showMenu) {
					$('.bgBody').removeClass('filter');
					$('.Shopping-Mall').slideUp()

				} else {
					$('.bgBody').addClass('filter');
					$('.Shopping-Mall').slideDown()

				}
				showMenu = !showMenu;
			}
		}
	}
</script>

<style scoped>
	.selectTitle {
		position: fixed;
		bottom: 10px;
		right: 30px;
		z-index: 1000;
	}
	
	.productContainer {
		height: 100%;
		overflow: auto;
	}
	
	.back_box {
		width: 20%;
		height: 1rem;
		line-height: 1.25rem;
		font-size: 0.34rem;
		float: left;
		color: rgba(255, 255, 255, 0.8);
	}
	
	.back_box img {
		width: 0.35rem;
		opacity: 0.8;
		float: left;
		margin-top: 0.44rem;
		margin-left: 0.25rem;
		vertical-align: middle;
	}
	
	.back_box span {
		height: 1rem;
		float: left;
	}
	
	.get_back p {
		width: 60%;
		height: 1rem;
		line-height: 1.2rem;
		text-align: center;
		float: left;
		font-size: 0.4rem;
	}
	/*商品区域*/
	
	#main {
		overflow-y: scroll;
		height: 100%;
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		padding-bottom: 30px;
		padding-top: 1.75rem;
	}
	
	.container-fluid {
		padding: .5rem;
		padding-bottom: 2rem;
		position: relative
	}
	
	.container-fluid li.oneproduct {
		/*float: left;*/
		box-sizing: border-box;
	}
	
	.container-fluid li.oneproduct .single img {
		max-width: 100%
	}
	
	.son {
		margin: 10px;
		background: red;
	}
	
	.noloaddate {
		position: absolute;
		bottom: 0;
		left: 0;
		text-align: center;
		width: 100%;
		color: #FFF;
		font-size: .75rem
	}
	
	.all_info {
		border: 3px solid rgba(255, 255, 255, .2);
	}
	
	.bg {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 9999;
		background: rgba(0, 0, 0, .3);
		display: none;
	}
	
	.productEditPop {
		z-index: 10000;
		display: none;
		font-size: .75rem;
		position: absolute;
		top: 30%;
		left: 50%;
		width: 5rem;
		margin-left: -2.5rem;
		background: rgba(255, 255, 255, .75);
		border-radius: 5px;
	}
	
	.productEditPop div {
		padding: .5rem 0;
		text-align: center;
		border-bottom: 1px solid #FFF;
		color: #0C6AFF;
	}
	
	.productEditPop div:last-child {
		border: 0px
	}
	
	.line p {
		font-size: 14px;
	}
	
	#footer {
		position: absolute;
		z-index: 2;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 48px;
		background: #444;
		padding: 0;
		border-top: 1px solid #444;
	}
	/*#scroller{height: 120%;}*/
	
	.appendBox {
		-webkit-column-count: 2;
		column-count: 2;
		-webkit-column-gap: 0.5em;
		column-gap: 0.5em;
		padding: 10px;
	}
	
	.white_bgg:after {
		clear: both;
	}
	
	.white_bgg.selected {
		background-color: green;
	}
	
	.white_bgg {
		-webkit-column-break-inside: avoid;
		padding: 5px;
		margin-top: 10px;
		background-color: rgba(255, 255, 255, 0.26);
	}
	
	.commodity {
		width: 100%;
	}
	
	.picture {
		width: 100%;
		display: block;
	}
	/*信息模块*/
	
	.message_box {
		width: 100%;
		/*margin: 0 auto 3%;*/
		background-color: rgba(255, 255, 255, 0.26);
	}
	
	.message {
		width: 90%;
		margin: 0 auto;
	}
	
	.message>div {
		height: 0.6rem;
		line-height: 0.8rem;
		overflow: hidden;
	}
	
	.message p {
		padding-top: 5px;
		color: rgba(255, 255, 255, 0.6);
	}
	
	.mold {
		width: 100%;
		overflow: hidden;
		padding: 5px 0;
	}
	
	.mold li:first-child {
		width: 59%;
		border-right: 1px solid rgba(255, 255, 255, 0.3);
	}
	
	.mold li:last-child {
		width: 40%;
		text-indent: 0.1rem;
	}
	
	.mold li {
		float: left;
		font-size: 0.25rem;
		color: rgba(255, 255, 255, 0.5);
	}
	
	.collect {
		width: 50%;
		height: 0.5rem;
		line-height: 0.5rem;
		float: right;
	}
	
	.collect li {
		width: 50%;
		font-size: 0.3rem;
		float: left;
		color: rgba(255, 255, 255, 0.5);
	}
	
	.collect li img {
		width: 0.33rem;
		margin-right: 0.05rem;
		vertical-align: middle;
	}
	
	#footer {
		position: absolute;
		z-index: 2;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 0.8rem;
		background-color: #222;
		background-image: -webkit-gradient(linear, 0 0, 0 100%, color-stop(0, #999), color-stop(0.02, #666), color-stop(1, #222));
		background-image: -moz-linear-gradient(top, #999, #666 2%, #222);
		background-image: -o-linear-gradient(top, #999, #666 2%, #222);
		padding: 0;
		border-top: 1px solid #444;
	}
	
	.Shopping-Mall {
		width: 100%;
		height: 100%;
		display: none;
		position: fixed;
		bottom: 0;
		left: 0;
		background-size: cover;
	}
	
	.shopping-mall-container {
		width: 80%;
		height: 70%;
		position: absolute;
		top: 12%;
		left: 10%;
		background: rgba(0, 0, 0, .5);
	}
	
	.Shopping-Mall-title {
		height: 10%;
		width: 100%;
		display: table;
		border-bottom: 1px solid rgba(255, 255, 255, .5);
	}
	
	.Shopping-Mall-title span {
		display: table-cell;
		vertical-align: middle;
	}
	
	.mallclass {
		float: left;
		width: 50%;
		height: 90%;
		box-sizing: border-box;
	}
	
	.mall-left-class {
		border-right: 1px solid rgba(255, 255, 255, .5);
	}
	
	.classification {
		padding: 10%;
		box-sizing: border-box;
	}
	
	.classification img {
		width: 25%;
		margin: 0 auto;
		display: block;
	}
	
	.classification p {
		margin-top: 10%;
	}
	
	.mall-left-class .classification {
		height: 33%;
	}
	
	.mall-left-class .classification img {}
	
	.mall-left-class .classification:nth-child(1),
	.mall-left-class .classification:nth-child(2) {
		border-bottom: 1px solid rgba(255, 255, 255, .5);
	}
	
	.mall-right-class .classification {
		height: 25%;
	}
	
	.mall-right-class .classification:nth-child(1),
	.mall-right-class .classification:nth-child(2),
	.mall-right-class .classification:nth-child(3) {
		border-bottom: 1px solid rgba(255, 255, 255, .5);
	}
	/*底部导航*/
	
	li {
		list-style: none;
	}
	
	.footer_navigation {
		width: 100%;
		height: 10%;
		position: fixed;
		bottom: 0;
		z-index: 10;
		background: url(../../../static/image/xl@2x.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.footer_navigation ul {
		height: 98%;
		line-height: 1;
	}
	
	.footer_navigation ul li:first-child {
		float: left;
	}
	
	.footer_navigation ul li:last-child {
		float: right;
	}
	
	.footer_navigation ul li img {
		vertical-align: middle;
	}
	
	.footer_navigation ul li.bottom_nav {
		width: 50%;
		margin: 0 auto;
		text-align: center;
		margin-left: -25%;
		left: 50%;
		position: absolute;
		height: 100%;
	}
	
	.bottom_nav img {
		width: 10%;
		margin-top: 10px;
		margin-bottom: 10px;
	}
	
	.bottom_nav span {
		display: block;
		font-size: 0.26rem;
		height: 0.25rem;
		line-height: 0.25rem;
		margin-top: 0.26rem;
		color: #fff;
	}
	
	.footer_navigation ul li.back_more {
		position: absolute;
		top: 50%;
		left: 10%;
	}
	
	.footer_navigation ul li.back_more img {
		width: 1.5em;
	}
	
	.bgBody.filter {
		filter: blur(3px);
	}
</style>


